<template>
    <div class="text-center ">
        <v-dialog v-model="localDialog">
   
      <div class="mx-auto to-blackMode" style="width: 900px; height: 800px;" >
        <v-toolbar  style="background-color: gray; color: aliceblue;">
              <v-toolbar-title >
                <v-icon style="margin-right: 5px;">
                  mdi-heart-outline
                  </v-icon>
                </v-toolbar-title>
                <v-btn
                  icon="mdi-close"
                  @click="closeDialog()"
                ></v-btn>
          </v-toolbar>
       
              
          <v-progress-linear
          color="cyan"
          indeterminate
          v-if="isLoading"
         ></v-progress-linear>
         <div class="d-flex child-flex justify-center align-center" v-if="!alarmData.length && !this.isLoading">
          <span style="font-size: 20px; opacity: 0.7; margin-top: 200px;">  .</span>
          </div>
        <div  v-if="alarmData.length && !this.isLoading">
             
          <v-list  lines="three" class="to-blackMode pa-3">
            <v-list-item>
           
              <v-list-item-content >
               
                <v-row v-for="(alarm, index) in alarmData" :key="index">
                  <v-col cols="1" >
                    <v-avatar :image="alarm.senderProfile" size="35" class="avatar"
                    v-if="alarm.senderProfile !== null" ></v-avatar>
                    <v-avatar :image="require('@/assets/ikmyung.png')"  
                     size="35" class="avatar" v-if="alarm.senderProfile === null" ></v-avatar>
                  </v-col>
                  <!--   -->
                  <v-col cols="8"  v-if="alarm.alarmType==='FOLLOWREQ'">                    
                      <span class="large-font" >
                        <span style="color: bisque; margin-right: 5px;">{{ alarm.sender }}</span>
                           .
                      </span>     
                      <span style="font-size: 12px; opacity: 0.7; margin-left: 10px;">{{alarm.createDate  }}</span>                 
                  </v-col>
                
                  <v-col cols="3" v-if="alarm.alarmType==='FOLLOWREQ'">
                    <span style="float: right;">
                    <v-btn  color="primary"  @click="handleFollowRequest(alarm.sender,'ACCEPT')" style="margin-right: 8px;"> </v-btn>
                    <v-btn  color="red"  @click="handleFollowRequest(alarm.sender,'REFUSE')"> </v-btn>
                  </span>
                  </v-col>
                   <!--    -->

                      <!--    -->
                  <v-col cols="11"  v-if="alarm.alarmType==='FOLLOW'">                    
                      <span class="large-font" >
                        <span style="color: bisque; margin-right: 5px;">{{ alarm.sender }}</span>
                           .
                      </span>     
                      <span style="font-size: 12px; opacity: 0.7; margin-left: 10px;">{{alarm.createDate  }}</span>                    
                  </v-col>
              
                    <!--     -->

                        <!--    -->
                  <v-col cols="8"  v-if="alarm.alarmType==='LIKE'">                    
                      <span class="large-font" >
                        <span style="color: bisque; margin-right: 5px;">{{ alarm.sender }}</span>
                           .
                      </span>         
                      <span style="font-size: 12px; opacity: 0.7; margin-left: 10px;">{{alarm.createDate  }}</span>         
                  </v-col>
                
                  <v-col cols="3"  v-if="alarm.alarmType==='LIKE'" >
                    <v-btn  color="grey" style="float: right;" @click="openPostDetailDialog(alarm.postNum)">  </v-btn>
                  </v-col>
                    <!--    -->


                    
                        <!--    -->
                  <v-col cols="8"  v-if="alarm.alarmType==='TAG'">                    
                      <span class="large-font" >
                        <span style="color: bisque; margin-right: 5px;">{{ alarm.sender }}</span>
                           .
                      </span>
                      <span style="font-size: 12px; opacity: 0.7; margin-left: 10px;">{{alarm.createDate  }}</span>                         
                  </v-col>
                  <v-col cols="3"  v-if="alarm.alarmType==='TAG'" >
                    <v-btn  color="green" style="float: right;" @click="openPostDetailDialog(alarm.postNum)">  </v-btn>
                  </v-col>
                    <!--    -->
                </v-row>
              </v-list-item-content>
            
            </v-list-item>
            
          </v-list>
          
       
          <div class="pagination-container">
            <v-pagination
              v-model="page"
              :length="totalPage"
              :total-visible=5           
            ></v-pagination>
        </div>
        </div>
      </div>
 
    </v-dialog>
    </div>
    <PostDetail 
    :value="postDetailDialog"
    :postNum="sendPostNum"
    @postDetailClose="closePostDetailDialog"
    @closeAlarm="closeDialog"
   ></PostDetail>
  </template>
<script>
import api from "@/api"
import PostDetail from "@/components/feed/post/PostDetail.vue"
import eventBus from "@/eventBus"
export default {
    name:'AlarmList',
    components:{
      PostDetail
    },
    props: {
       value: {//     
         type: Boolean,
         required: true
       }     
      },
      computed:{
    //     (true/false)  , props value   .
     localDialog:{
         get(){
       
             return this.value 
         }
     }
  },
  watch:{
    value(){
        this.alarmGet()
    },
     //   
     page(){
      //   
      this.alarmGet()
    }
  },
  data(){
    return{
        alarmData:[], // 
        page:1, // 
        totalPage:0, // 
        postDetailDialog:false, //        ,   
        sendPostNum:0, //  
        isLoading:false // 
    }
  },
  methods:{
    //     
    openPostDetailDialog(postNum){
      
      this.sendPostNum=postNum
      this.postDetailDialog=true
    },
    //   
    closePostDetailDialog(){
      this.postDetailDialog=false
    },
    //   
    closeDialog(){
      //   
        this.isLoading=false
        this.alarmData=[]
        this.page=1
        this.totalPage=0
        this.postDetailDialog=false
        this.sendPostNum=0
        eventBus.emit('getAlarmData')
        this.$emit('closeAlarm');
    },
    //    
    alarmGet(){
      //  
      if(this.isLoading) return
      //  true
      this.isLoading=true
      //api
      api.get(`/alarm/${this.page}`)
      .then((res)=>{
        // 
        this.alarmData=res?.data?.reqData?.list
        // [] 
        if(this.alarmData==null){
          this.alarmData=[]
        }
        // 
        this.totalPage=res?.data?.reqData?.totalPage
      })
      .catch((err)=>{
        if(err?.response?.data?.message){
        alert(err?.response?.data?.message)  
        }
        this.closeDialog()
      })
      .finally(()=>{
        this.isLoading=false
      })
    },
    //    , 
    handleFollowRequest(nickname,type){
      // , or   
      api.post('/members/following',{
        nickname:nickname,
        type:type
      })
      .then(()=>{
        //  
        this.alarmGet()
      })
      .catch((err)=>{
        if(err?.response?.data?.message){
        alert(err?.response?.data?.message)  
        }
      })
    }
  }
}
</script>
<style >
   .dialog-container {
    width: 600px;
    height: 700px;
    position: relative;
    background-color: white; /*    */
}



.dialog-content {
    display: flex;
    flex-direction: column;
    height: 100%;
    position: relative;
}


.pagination-container {
    position: absolute;
    bottom: 10px;
    left: 0;
    right: 0;
    text-align: center;
    padding: 0 10px; /* Optional: Add padding for spacing */
}
</style>