<template>
   
   <!--   -->
   
   <v-dialog v-model="localDialog"  max-width="500" >
      
      <v-card  
        prepend-icon="mdi-account"
        title=" "
        class="pa-3"   
      >

      <v-divider></v-divider>
        <v-progress-linear
          color="black"
          indeterminate
          v-if="isLoading"
         ></v-progress-linear>
    <v-card-text class="mt-6" v-show="isAuth" >

      <table width="400px" style="border:1px #D3D3D3 solid;">
        <tr >
          <th style="border-bottom: 1px #D3D3D3 solid;"></th>
          <th style="border-bottom: 1px #D3D3D3 solid;"> </th>
        </tr>
        
        <tr class="text-center" >
          <td><span style="font-weight: bold; "> {{ findEmail }} </span></td>
          <td><span style=" opacity: 0.7; ">{{ findCreateDate }}</span></td>
        </tr>
      </table>

 
    </v-card-text>
           <v-card-text class="mt-6" v-show="!isAuth">
              <div class="text-subtitle-1 text-medium-emphasis"> </div>
                 <v-row>
                      <v-col cols="8">
                            <v-text-field 
                               density="compact"
                               placeholder=""
                               prepend-inner-icon="mdi-account"
                               variant="outlined"
                               v-model="nickname"
                              :disabled="isLoading"
                               :rules="nickNameRules"
                               required
                               :error-messages="nickNameErrors"
                            ></v-text-field>
                      </v-col>
                 </v-row>

        <div class="text-subtitle-1 text-medium-emphasis d-flex align-center justify-space-between"></div>
   
        <v-row>
            <v-col cols="7">
                  <v-text-field
                   :rules="nameRules"
                   required
                   :error-messages="nameErrors"
                   density="compact"
                   placeholder=""
                   prepend-inner-icon="mdi-account"
                   variant="outlined"
                   v-model="name"
                   :disabled="isLoading"
                  ></v-text-field>
             </v-col>
         </v-row>    

          <div class="text-subtitle-1 text-medium-emphasis d-flex align-center justify-space-between">
          
          </div>

        <v-row>
            <v-col cols="4">
                <v-select
                  density="compact"               
                  variant="outlined"
                  v-model="firstPhoneNum"
                  :items="items"             
                  required          
                  :disabled="isLoading"   
                ></v-select>
           </v-col>

            <v-col cols="4">             
                  <v-text-field               
                    type="text"
                    density="compact"
                    maxlength="4" 
                    v-model="middlePhoneNum"
                    variant="outlined"      
                    :disabled="isLoading"   
                  ></v-text-field>
            </v-col>

              <v-col cols="4">
                  <v-text-field
                    maxlength="4" 
                    type="text"
                    density="compact"
                    variant="outlined"
                    v-model="lastPhoneNum"
                    :disabled="isLoading"
                  ></v-text-field>
              </v-col>

        </v-row>
         
  </v-card-text>

    
        <v-card-actions>

          <v-spacer></v-spacer>
          
          <v-btn
            text="Close"
            variant="plain"
            :disabled="isLoading"
            @click=closeDialog()> 
          </v-btn>

          <v-btn
          v-show="!isAuth"
            color="primary"
            text="Submit"
            variant="tonal"
            :disabled="isLoading"
            @click=sumbmitFindId()
          ></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </template>
  
  <script>
  import api from '@/api';
  export default {
    name: 'FindIdComponent',
  

 props: {
      //     
      value: {
        type: Boolean,
        required: true
      }
    
},
data(){
    return{
        items:['010','011','016','017','019'],//     
        firstPhoneNum:'010', //  010 
        middlePhoneNum:'',//  
        lastPhoneNum:'',//  
        name:'',//
        nickname:'',//
        findEmail:'',// 
        findCreateDate:'',
        isAuth: false, //  true            .                
        isLoading:false,//
        //  
         nickNameRules:[
         v => !!v || '  .', //    
         v => v.trim().length > 0 || '    .', //   
         v => !/\s/.test(v) || '   .', //  
        //  v => !/[-|-|-]/.test(v) || '    .', //    = >   
        //  v => /^[a-zA-Z0-9_ ]+$/.test(v) || ' _     .', //      
        //  v => v.length <= 15 || '  15   .', //        
            
         ],
        nameRules:[
        v => !!v || '  .', //    
        v => v.trim().length > 0 || '    .', //   
        v => !/\s/.test(v) || '   .', //  
        ],  
        //
        nameErrors:[],
        nickNameErrors:[],
      }
  },
  computed:{
    localDialog:{
        get(){
            return this.value //     (true/false)  , props value   .
        }
    }
 },      
   methods: {
     //rules   
      validateField(value, rules) { 
          return rules
            .map(rule => rule(value))
            .filter(error => typeof error === 'string');
        },
      //  
      closeDialog() {
        //
        this.isAuth=false;//         
        this.nickname='';// 
        this.name=''; // 
        this.firstPhoneNum='010' //  3 
        this.middlePhoneNum='' //
        this.lastPhoneNum='' // 
        this.$emit('findIdClose');//     
      
      },
      //   
      sumbmitFindId(){
        //  true return
        if(this.isLoading===true){
            return
        }

        this.nameErrors = this.validateField(this.name, this.nameRules); //
        this.nickNameErrorsnameErrors = this.validateField(this.nickname, this.nickNameRules); //

        if (this.nameErrors.length > 0 || this.nickNameErrors.length>0  ) { //    
        
            return;// axios    
        }

        //  
        const fullPhoneNum= (this.firstPhoneNum+this.middlePhoneNum+this.lastPhoneNum); 

                //   validation
                if (fullPhoneNum.length !== 11) {
                  alert('  11 .');
                  return;
                }

                //    
                if (/[-]/.test(fullPhoneNum)) {
                  alert('     .');
                  return;
                }

                //    
                if (/\s/.test(fullPhoneNum)) {
                  alert('     .');
                  return;
                }

                //      
                if (!/^\d+$/.test(fullPhoneNum)) {
                  alert('    .');
                  return;
                }

              //   
              this.isLoading=true;    

              //  api 
              api.post('/members/findEmail',{
                name:this.name,//
                nickname:this.nickname,//
                phone:fullPhoneNum// 
              })
              .then((res)=>{  
                //
                  this.findEmail=res?.data?.reqData?.email;//   
                  this.findCreateDate=res?.data?.reqData?.vueCreateDate//   
                  this.isAuth=true; //    
              })
              .catch((err)=>{
                //     
                if(err?.response?.data?.message){
                  alert(err?.response?.data?.message)  
                  }
                    
              })
              .finally(()=>{
                this.isLoading=false; //  false
              })

      }
    }
  }
  </script>