<template>
  <v-card-text >
    
      
      <!--    -->
      <v-text-field    
        :class="isSearchBarOpen?' globalaBarInSearch':''"
        append-inner-icon="mdi-magnify"
        density="compact"
        label=" "
        hide-details
        variant="solo"
        single-line
        @keydown="handleSearchKeyDown"      
        v-model="keyword"     
      ></v-text-field>

     

    <!--     -->
     <GlobalSearchResult 
        v-show="isSearchBarOpen"
        @searchHobby="searchHobby"
        @searchMember="searchMember"
        @closeSearch="closeSearch"
        :keyword="keyword"
        style="background-color: white; z-index: 1000000; pointer-events: auto;"
        ref="globalSearchResult"></GlobalSearchResult>
   
  </v-card-text>
</template>

<script>

export default {
  data() {
    return {
      keyword:'', //  
      loading: false, //   
      isSearchBarOpen:false //  
      
    }
  },
  // 
watch:{
  //  
  keyword(keyword){
    
    //   
    if(keyword!==''){
      //   
      this.isSearchBarOpen=true
    }
  }
},
  methods: {
   
//( )        
handleSearchKeyDown(event) {
switch(event.key) {
  case 'ArrowDown'://  
    this.handleSearchArrowDown();
    break;
  case 'ArrowUp'://
    this.handleSearchArrowUp();
    break;
  case 'Enter'://
  event.preventDefault(); // textarea     
    this.handleSearchEnter();
    break;
  default:
    break;
}
},
// ref()     
handleSearchArrowDown(){

this.$refs.globalSearchResult.handleArrowDown();
},
handleSearchArrowUp(){
this.$refs.globalSearchResult.handleArrowUp();
},
handleSearchEnter(){
  
this.$refs.globalSearchResult.handleEnter();
},
//     value    
//  
searchHobby(hobby){
  //  
  this.isSearchBarOpen=false
  //   
  this.$router.push({
      name: 'searchfeed',
      params: { type: 'hobby', keyword: hobby }
    });
},
//  
searchMember(member){
  //   
  this.isSearchBarOpen=false
  //    
  this.$router.push({
      name: 'searchfeed',
      params: { type: 'member', keyword: member }
    });
},
//    
closeSearch(){
 this.keyword=''
this.isSearchBarOpen=false
}
    
  }
}
</script>

<style scoped>
.globalaBar{
    background-color: aliceblue;
    
    border-radius: 8px 8px 8px 8px 
  }
  .globalaBarInSearch{
    background-color: aliceblue;
    border-radius: 8px 8px 0 0
  }
  .v-text-field {
    width: 100%; /*       */
  }
  .test{
    pointer-events: none;
  }
  
  
  .v-list-item {
    cursor: pointer;
  }
  
  .v-list-item:hover {
    background-color: #f0f0f0; /*    hover  */
  }
  

</style>