<template>
    <div class="text-center ">
      <v-dialog
        v-model="localDialog"
        max-width="600"
      >
        <v-progress-linear
          color="cyan"
          indeterminate
          v-if="isLoading"
         ></v-progress-linear>
        <v-card
          prepend-icon="mdi-account"
          title=" "
          class="to-blackMode"
        >
          <v-card-text>
            
            <div class="text-subtitle-1 text-medium-emphasis d-flex align-center justify-space-between "
            >
               <span style="color: aliceblue;"></span>
            </div>
            
        <!---->
        <v-row>
            <v-col cols="7">
                <v-text-field
                  density="compact"
                  placeholder=""
                  prepend-inner-icon="mdi-account"
                  variant="outlined"               
                  required
                  v-model="name"
                  :rules="nameRules"
                  :error-messages="nameErrors" 
                  :disabled="isLoading"             
                ></v-text-field>
           </v-col>
        </v-row>

            <div class="text-subtitle-1 text-medium-emphasis">
                <span style="color: aliceblue;">Email</span>
            </div>
                 <v-row>
                    <v-col>
                        <v-text-field
                        density="compact"
                        placeholder="Email"
                        prepend-inner-icon="mdi-email-outline"
                        variant="outlined"
                        v-model="email"
                        :rules="emailRules"
                        required
                        :error-messages="emailErrors"
                        :disabled="isLoading"
                        ></v-text-field>
                    </v-col>    
                 </v-row>


            <div class="text-subtitle-1 text-medium-emphasis d-flex align-center justify-space-between">
              <span style="color: aliceblue;"></span>
            </div>

        <!---->
        <v-row>
            <v-col cols="12">
              <v-text-field
                :append-inner-icon="visible ? 'mdi-eye-off' : 'mdi-eye'"
                :type="visible ? 'text' : 'password'"
                density="compact"
                placeholder=""
                prepend-inner-icon="mdi-lock-outline"
                variant="outlined"
                @click:append-inner="visible = !visible"
                v-model="password"
                :rules="passwordRules"
                required
                :error-messages="passwordErrors"
                :disabled="isLoading"
              ></v-text-field>
            </v-col>
        </v-row>
   
    
    </v-card-text>
          <v-divider></v-divider>
            <v-card-actions>
              <v-spacer></v-spacer>
                    <v-btn
                      text="Close"
                      variant="plain"
                      @click="closeDialog()"
                      :disabled="isLoading"
                    ></v-btn>
          
                    <v-btn
                      color="red"
                      text="Delete Account"
                      variant="tonal"
                      @click="memberDelete()"
                      :disabled="isLoading"
                    ></v-btn>
            </v-card-actions>
        </v-card>
      </v-dialog>
    </div>
  </template>

  <script>
import api from '@/api';

 
export default {
    name: 'DeleteVerify',
  

    props: {
      value: {//      
        type: Boolean,
        required: true
      }
    
},data(){
    return{
        name:'',// 
        email: '', //
        password:'', //
            emailRules: [
                v => !!v || '  .', //   
                v => v.trim().length > 0 || '   .', //   
                
                v => /.+@.+\..+/.test(v) || '   .' //@ .   
             ],
            passwordRules: [
                    v => !!v || '  .', //   
                    v => v.trim().length > 0 || ' .', //  
                    v => !/\s/.test(v) || '   .', //  

             ],
              nameRules:[
              v => !!v || '  .', //    
              v => v.trim().length > 0 || '    .', //   
              v => !/\s/.test(v) || '   .', //  
              ],
        passwordErrors:[],// 
        emailErrors: [],//  
        nameErrors:[],//  
        visible:false,
        isLoading:false// 
    }
    },
    computed:{
      //     (true/false)  , props value   .
        localDialog:{
            get(){
                return this.value 
            }
        }
   },      
    methods: {
      //rules   
      validateField(value, rules) { 
             return rules
            .map(rule => rule(value))
            .filter(error => typeof error === 'string');
        },
      closeDialog() {
        this.password='',// 
        this.name='',// 
        this.email='', //  
        this.passwordErrors=[],//   
        this.nameErrors=[],//  
        this.emailErrors=[],//   
        this.isLoading=false,// false
        

        this.$emit('DeleteVerifyClose');//      
      },
      //   
      memberDelete(){
        //  
        if(this.isLoading===true){
            return
        }

        this.passwordErrors = this.validateField(this.password, this.passwordRules); //   
        this.nameErrors = this.validateField(this.name, this.nameRules); //   
        this.emailErrors = this.validateField(this.email, this.emailRules); //   

          if (this.passwordErrors.length > 0 ||this.nameErrors.length>0||this.emailErrors.length>0) { //       return
          
            return;// axios   
        

          }

          this.isLoading=true
          //   API 
    api.delete('/setting', {
        data: {
            name: this.name,
            password: this.password,
            email: this.email
        }
    })
    .then(() => {
        //     access  
        localStorage.removeItem('access');

        //  
        alert('  .    .');

        //     
        this.$router.push('/login').then(() => {
            //         
        }).catch(err => {
            console.error('    :', err);
        });
    })
    .catch(err => {
        //      
        if(err?.response?.data?.message){
        alert(err?.response?.data?.message)  
        }
    })
    .finally(()=>{
      this.isLoading=false
    })


      }

    }
  }
  </script>