<template>
 
    <!---->
    <div>
        <v-img
          class="mx-auto my-6"
          max-width="150"
          src="@/assets/logo2.png"
        ></v-img> 
    </div>
  
    <v-card
      class="mx-auto pa-12 pb-8"
      elevation="8"
      max-width="448"
      rounded="lg"
    >
  
      <!-- -->
    

        <!---->
        <div class="text-subtitle-1 text-medium-emphasis">Email</div>

        <v-text-field
          density="compact"
          placeholder="Email"
          prepend-inner-icon="mdi-email-outline"
          variant="outlined"
          v-model="email"
          :rules="emailRules"
          required
          :error-messages="emailErrors"
          :disabled="isLoading"
        ></v-text-field>

        <!---->
        <div class="text-subtitle-1 text-medium-emphasis d-flex align-center justify-space-between">
          
        </div>

        <v-text-field
          :append-inner-icon="visible ? 'mdi-eye-off' : 'mdi-eye'"
          :type="visible ? 'text' : 'password'"
          density="compact"
          placeholder=""
          prepend-inner-icon="mdi-lock-outline"
          variant="outlined"
          @click:append-inner="visible = !visible"
          v-model="password"
          :rules="passwordRules"
          required
          :error-messages="passwordErrors"
          :disabled="isLoading"
        ></v-text-field>
     
        <!--  -->
        <v-btn
          class="mb-8"
          color="blue"
          size="large"
          variant="tonal"
          block
          @click="login"
          :disabled="isLoading"
        >
          Log In
        </v-btn>
 
        <v-progress-linear
          color="black"
          indeterminate
          v-if="isLoading"
         ></v-progress-linear>
      <v-card-text class="text-center">

        <!--   true -->
            <button
              class="text-caption text-decoration-none text-blue"
              @click="openFindIdDialog"
              :disabled="isLoading"
            >
             ?
            </button>

        <!---->
        &nbsp;&nbsp;&nbsp;/ &nbsp;&nbsp;&nbsp;

        <!--   true -->
        <button
          class="text-caption text-decoration-none text-blue"
          @click="openFindPwdDialog"
          :disabled="isLoading"
        >
         ?
        </button>
      </v-card-text>

        <v-card-text class="text-center">
          <!--  -->
            <router-link to="/join">
                <button class="text-blue text-decoration-none"
                  :disabled="isLoading"
                >
                  <v-icon icon="mdi-chevron-right"></v-icon>

                </button>
            </router-link>
        </v-card-text>
    </v-card>
    <!--()  -->
    <FindIdComponent v-model:value="findIdDialog" @findIdClose="closeFindIdDialog" />
    <!--  () -->
    <FindPwdComponent v-model:value="findPwdDialog" @findPwdClose="closeFindPwdDialog" />

</template>

<script>
import FindIdComponent from "@/components/member/FindId.vue"
import FindPwdComponent from "./FindPwd.vue";
import api from "@/api"

export default {
  data: () => ({
    visible: false,// **  or  
    email: '',//
    password: '',//
    //(validation)
    emailRules: [
      v => !!v || '  .', //   
      v => v.trim().length > 0 || '   .', //   
      v => /.+@.+\..+/.test(v) || '   .' //@ .   
    ],
    passwordRules: [
      v => !!v || ' .', //   
      v => v.trim().length > 0 || ' .', //  
    ],
   
    emailErrors: [], //validtion   
    passwordErrors: [],//validation   
    findIdDialog: false, //    
    findPwdDialog: false,//    
    isLoading:false// 
  }),
  components: {
    FindIdComponent,//  
    FindPwdComponent//  
  },
  methods: {
    //rules  
    validateField(value, rules) {  
      return rules
        .map(rule => rule(value))
        .filter(error => typeof error === 'string');
    },
    //    
    closeFindIdDialog() { 
      this.findIdDialog = false;
    },
    //    
    openFindIdDialog() {
      this.findIdDialog = true;
    },
    //     
    closeFindPwdDialog() {
      this.findPwdDialog = false;
    },
    //    
    openFindPwdDialog() {  
      this.findPwdDialog = true;
    }, 
    //  
    login() { 
      // true return
      if(this.isLoading===true){
            return
      }
      //   
      this.emailErrors = this.validateField(this.email, this.emailRules); 
      //   
      this.passwordErrors = this.validateField(this.password, this.passwordRules);

      //      0  return
      if (this.emailErrors.length > 0 || this.passwordErrors.length > 0) { 

        return;// axios    
      }

      // ,    

      //formdata email  .
      const formdata = new FormData();
      //  username request    key username
      //   email
      formdata.append('username', this.email);
      //
      formdata.append('password', this.password);

      //   (  true )
      this.isLoading=true 

      // api 
     
      api.post('/login', formdata)
      .then((res) => { 
          
          // 
                //   access     
                const accessToken = res?.headers['access'];
                // access  
                localStorage.setItem('access', accessToken); 

                //   url  ==> why?   url      
                //login              
                const reqUrl = localStorage.getItem('requestUrl');

                //   / ( default)
                if (!reqUrl) {
                  this.$router.push('/');
                } else {
                //   
                this.$router.push(reqUrl);
              }
             
        })
        .catch((err)=>{
        //    
       
        if(err?.response?.data?.message){
        alert(err?.response?.data?.message)  
        }
       
        })
        .finally(()=>{
          //  
          this.isLoading=false
        })
       
    }
  }
}
</script>
