<template>
    <div class="text-center ">
      <v-dialog
        v-model="localDialog"
        max-width="600"
      >
       <v-progress-linear
          color="cyan"
          indeterminate
          v-if="isLoading"
         ></v-progress-linear>
        <v-card
          prepend-icon="mdi-account"
          title=" "
          class="to-blackMode"
        >
          <v-card-text>
            <div class="text-subtitle-1 text-medium-emphasis d-flex align-center justify-space-between">
               <span style="color: aliceblue;">  </span>
            </div>

        <!---->
        <v-row>
            <v-col cols="12">
              <v-text-field
                :append-inner-icon="visible ? 'mdi-eye-off' : 'mdi-eye'"
                :type="visible ? 'text' : 'password'"
                density="compact"
                placeholder=" "
                prepend-inner-icon="mdi-lock-outline"
                variant="outlined"
                @click:append-inner="visible = !visible"
                v-model="password"
                :rules="passwordRules"
                required
                :error-messages="passwordErrors"
                :disabled="isLoading"
              ></v-text-field>
            </v-col>
        </v-row>
   
              <div class="text-subtitle-1 text-medium-emphasis d-flex align-center justify-space-between">
                 <span style="color: aliceblue;"> </span>
              </div>

        <!-- -->
        <v-row>
            <v-col cols="12">
              <v-text-field
                :append-inner-icon="visible ? 'mdi-eye-off' : 'mdi-eye'"
                :type="visible ? 'text' : 'password'"
                density="compact"
                placeholder=" "
                prepend-inner-icon="mdi-lock-outline"
                variant="outlined"
                @click:append-inner="visible = !visible"
                v-model="newPassword"
                :rules="newPasswordRules"
                required
                :error-messages="newPaasswordErrors"
                :disabled="isLoading"
              ></v-text-field>
             </v-col>
         </v-row>

    </v-card-text>
  
          <v-divider></v-divider>
  
          <v-card-actions>
            <v-spacer></v-spacer>
  
                <v-btn
                  text="Close"
                  variant="plain"
                  @click="closeDialog()"
                  :disabled="isLoading"
                ></v-btn>
      
                <v-btn
                  color="primary"
                  text="Save"
                  variant="tonal"
                  @click="submitPasswordChange()"
                  :disabled="isLoading"
                ></v-btn>

          </v-card-actions>
        </v-card>
      </v-dialog>
    </div>
  </template>

  <script>
  import api from '@/api';
export default {
    name: 'ChangerPwd',
  

    props: {
      value: {//      
        type: Boolean,
        required: true
      }
    
      },
      data(){
        return{
        password:'',//
        newPassword:'',//  
        passwordRules: [//  =>    validation 
                v => !!v || '  .', //   
                v => v.trim().length > 0 || ' .', //  
                v => !/\s/.test(v) || '   .', //  

              ],
        
        newPasswordRules: [//   
                v => !!v || '  .', //   
                v => v.trim().length > 0 || ' .', //  
                v => !/\s/.test(v) || '   .', //  
                v => (v && v.length >= 8) || '  8  .', //  8
                v => (v && v.length <= 20) || '  20   .', //  20
                v => /[a-zA-Z]/.test(v) || '   ()  .', //  
                v => /\d/.test(v) || '     .', //  
                v => /[!@#$%^&*(),.?":{}|<>]/.test(v) || '     .', //  
              ],
        newPaasswordErrors:[],//   
        passwordErrors:[],//    
        visible:false,
        isLoading:false//   
     }
      } ,
     computed:{
      //      (true/false)  , props value   .
          localDialog:{
              get(){
                  return this.value 
              }
          }
     },  
  
    methods: {
      //rules   
      validateField(value, rules) { 
          return rules
            .map(rule => rule(value))
            .filter(error => typeof error === 'string');
        },
        //   
      closeDialog() {
        this.password='',// 
        this.newPassword='',//  
        this.isLoading=false,//  
        this.passwordErrors=[],//   
        this.newPaasswordErrors=[]//    


        this.$emit('changePwdClose');//      
      },
      //   
      submitPasswordChange(){

        //   true return 
        if(this.isLoading===true){
            return
          }

        this.passwordErrors = this.validateField(this.password, this.passwordRules); //   
        this.newPaasswordErrors = this.validateField(this.newPassword, this.newPasswordRules); //    

          //       return
          if (this.passwordErrors.length > 0 ||this.newPaasswordErrors.length>0) { 
          
            return;// axios   
         
          }
                  //form-data    append
                  const formdata= new FormData();
                  formdata.append('password',this.password)
                  formdata.append('newPassword',this.newPassword)

                  this.isLoading=true;//    

                        //   api
                        api.put("/setting/pwdChange",formdata) //       formdata  @requestbody   
                        .then(()=>{
                        //      
                        alert("  .   .")
                        this.closeDialog()//  

                                  //   then   api
                                  api.post('/logout')
                                  .then(()=>{

                                      localStorage.removeItem('access')//   
                                      this.$router.push('/login');//  
                                    })
                                  .catch((err)=>{
                                      //       
                                      if(err?.response?.data?.message){
                                          alert(err?.response?.data?.message)  
                                      }
                                    
                                  })
                                  .finally(()=>{
                                    this.isLoading=false;//  false 
                                  })
                       
                          })
                          .catch((err)=>{
                            //        
                            if(err?.response?.data?.message){
                                alert(err?.response?.data?.message)  
                            }
                            
                          })
                          .finally(()=>{
                            this.isLoading=false;//  
                          })
      
      }

    }
  }
  </script>