<template>
  
    <!-- /  -->
    <v-dialog v-model="localDialog">
      <div ref="container">
      <v-card class="mx-auto to-blackMode" width="600">
        
       

        <v-btn
          style="margin-left: 560px; margin-top: 10px; background-color: gray;"
          icon
          class="close-btn"
          size="30"
          @click="closeDialog()"
          v-if="!isLoading"
        >
          <v-icon>mdi-close</v-icon>
        </v-btn>

        <span v-if="!isLoading" style="display: flex; font-size: 20px; align-items: center; justify-content: center;">
          {{ flwType }}
        </span>

        <v-col style="display: flex; align-items: center; justify-content: center;">
          <v-divider></v-divider>        
        </v-col>

          <div v-if="!FollowData.length && !isLoading" class="no-data-message">
            <span>   .</span>
          </div>

        <div class="scroll-container" >
             
          <v-list  lines="three" class="to-blackMode pa-3">
            <v-list-item>
           
              <v-list-item-content >
               
                <v-row v-for="(follow, index) in FollowData" :key="index">
                  <v-col cols="2"  style="cursor:pointer" @click="goOtherFeed(follow.nickname)">
                    <v-avatar :image="follow.profile" size="35" class="avatar"
                    v-if="follow.profile !== null" ></v-avatar>
                    <v-avatar :image="require('@/assets/ikmyung.png')"  
                     size="35" class="avatar" v-if="follow.profile === null" ></v-avatar>
                  </v-col>

                  <v-col cols="6" style="cursor:pointer" @click="goOtherFeed(follow.nickname)">
                     
                      <span class="large-font" >{{ follow.nickname }}</span>
                   
                  </v-col>

                  <v-col cols="4" v-if="follow.myApprove === null && follow.nickname !== myNickname" class="d-flex align-center justify-center">
                    <v-btn  color="primary"  @click="doFollow(follow.nickname)"> FOLLOW </v-btn>
                  </v-col>

                  <v-col cols="4" v-if="follow.myApprove === 'FOLLOWOK' && follow.nickname !== myNickname" class="d-flex align-center justify-center">
                    <v-btn  color="red" @click="unFollow(follow.nickname)">UNFOLLOW</v-btn>
                  </v-col>

                  <v-col cols="4" v-if="follow.myApprove === 'FOLLOWNO' && follow.nickname !== myNickname" class="d-flex align-center justify-center">
                    <v-btn  color="grey"  @click="unFollow(follow.nickname)">Follow </v-btn>
                  </v-col>
                </v-row>
              </v-list-item-content>
            
            </v-list-item>
            
          </v-list>
          
          <div v-if="isLoading" class="loading-indicator">
            <v-progress-circular indeterminate color="primary" size="30"></v-progress-circular>
          </div>
          <div ref="sentinel" class="sentinel"></div>
        </div>
      </v-card>
    </div>
    </v-dialog>

</template>

<script>
import api from '@/api';

export default {
  name: 'FlwListComponent',

  props: {
    // 
    value: {
      type: Boolean,
      required: true
    },
    // OR  
    flwType: {
      type: String,
      required: true
    },
    // 
    nickname: {
      type: String,
      required: true
    },
    //    
    itsMe: {
      type: Boolean,
      required: true
    }
  },

  data() {
    return {
      isLoading: false, //   
      FollowData: [], ///   (    )
      page: 1, //
      myNickname: '', //    == >     
      observer: null,  //intersection Observer  - > 
      isNomoreData:false //        
    };
  },
//  
  watch: {
    //  open    (, )
    value(newVal) {
      if (newVal) {
        this.initObserver(); // IntersectionObserver 
        this.callFollowList(); // 1   
      }
    }
  },

  beforeUnmount() {
    //     
    if (this.observer) {
      this.observer.disconnect();
    }
  },

  computed: {
    // open, close 
    localDialog: {  
      get() {
        return this.value;
      }
    }
  },

  methods: {
    // intersection observer  
    initObserver() {
      this.$nextTick(() => {
        const sentinel = this.$refs.sentinel; //    
        const container = this.$refs.container; //        container   ref
       
        if (sentinel && container) {
          this.observer = new IntersectionObserver((entries) => {
            
            entries.forEach(entry => {
              if (entry.isIntersecting) {
                // sentinel    
                this.callFollowList(); // Sentinel     
                this.page++;//  
              }
            });
          }, {
            root: container, // container root 
            rootMargin: '0px',
            threshold: 1.0 // Sentinel    
          });

          this.observer.observe(sentinel); // Sentinel  
        }
      });
    },

    //   
    closeDialog() {
      // 
      if (this.observer) {
        this.observer.disconnect();
      }
      // 
      this.FollowData = [];
      this.page=1
      this.isNomoreData=false
      //  emit 
      this.$emit('flwListClose');
    },
    ///    
    callFollowList() {
      //  ,     return
      if (this.isLoading || this.isNomoreData) {
        return;
      }
      // true
      this.isLoading = true;
      ///    /  ==> pathvariable
      api.get(`/feed/userfeed/${this.nickname}/${this.flwType}/${this.page}`)
        .then((res) => {
          //    noMoreDate  true   axios   
          if(res?.data?.reqData?.followList?.length===0){
            this.isNomoreData=true;
          }
          // 
          else{ 
            //   
            this.FollowData = [...this.FollowData, ...res?.data?.reqData?.followList];
            //  (      )
            this.myNickname = res?.data?.reqData?.myNickname;
          }
         
        })
        .catch((err) => {
          if(err?.response?.data?.message){
        alert(err?.response?.data?.message)  
        }
          this.closeDialog();
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    //  ( , /   )
    doFollow(nickname) {
      //  true 
      this.isLoading = true;
      //api 
      api.post("/feed/follow", {
        nickname: nickname, //      
      })
        .then((res) => {
          //        ( dom   )
         this.updateFollowData(nickname,res?.data?.reqData) //  ,       
        })
        .catch((err) => {
          if(err?.response?.data?.message){
        alert(err?.response?.data?.message)  
        }
        })
        .finally(()=>{
          this.isLoading=false
        });
    },
    // 
    unFollow(nickname){
      //  
      this.isLoading=true;
      api.delete("/feed/follow",{
        data:{
          nickname:nickname //   
        }
       
      })
      .then(()=>{
        //        (  null  )
        // = > why null?    FOLLOWOK , FOLLOWNO     
        //       NULL  
        this.updateFollowData(nickname,null)
      })
      .catch((err)=>{
        if(err?.response?.data?.message){
        alert(err?.response?.data?.message)  
        }
        this.isLoading=false
      })
      .finally(()=>{
        this.isLoading=false
      })
    },
    // FollowData   
  updateFollowData(nickname, changeState) {
    //   
    this.FollowData = this.FollowData.map(follow => {
      //    
      // !!!       ,     .
      if (follow.nickname === nickname) {
        return {
          ...follow,
          myApprove: changeState //   == > DOM  
        };
      }
      return follow;
    });
  },
  //     
  goOtherFeed(nickname) {//  

      this.closeDialog(); //   = > /   
      this.$nextTick(() => {
        this.$router.push({ name: 'userfeed', params: { nickname: nickname } }); //   (  param)
      });
    }
  }
};
</script>

<style>
.large-font {
  font-size: 18px;
}

.scroll-container {
  max-height: 500px;
  overflow-y: auto;
}

.sentinel {
  height: 20px;
}

.loading-indicator {
  text-align: center;
  padding: 10px;
  color: #888;
}

.scroll-container::-webkit-scrollbar {
  width: 12px;
}

.scroll-container::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 10px;
}

.scroll-container::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 10px;
}

.scroll-container::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.no-data-message {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  color: gray;
  font-size: 16px;
  text-align: center;
}

</style>
