<template lang="">
    <div class="fixed-tabs" >
        <v-tabs
        class="to-blackMode"
        fixed-tabs
        v-model="selectedTab"
        >
            <v-tab value="hobby" @click="changeType('hobby')" > </v-tab>
            <v-tab value="member" @click="changeType('member')" > </v-tab>
        </v-tabs>     
    </div>
   
    <div style="margin-top:30px;">          
      <SearchHobby v-if="type==='hobby'"
      :keyword="keyword"
        :type="type"
      ></SearchHobby>

      <Searchpeople v-if="type==='member'"
      :keyword="keyword"
        :type="type"
      ></Searchpeople>
    </div>
</template>
<script>

import SearchHobby from './SearchHobby.vue';  //  
import Searchpeople from './Searchpeople.vue'; //  

export default {
  props:{
    type:String, //   
    keyword:String //  
  },
  watch: {
    //    (   )
    type(newType) {
      this.selectedTab = newType; // prop    
    }
  },
  data(){
return{
selectedTab: this.type // selected   
}
  },
  methods:{
    //  ,   
 changeType(newtype){
  //    
  this.$router.push({
      name: 'searchfeed',
      params: { type: newtype, keyword: this.keyword }
    });
 }
  },
    components:{
        SearchHobby,
        Searchpeople
    }
}
</script>
<style scoped>
.content {
  margin-top: 56px; 
}
</style>