<template >
    <div >
      

      <v-card
        class="mx-auto pa-16 pb-8 card-scroll mt-13" 
        elevation="15"
        max-width="600"
        rounded="lg"
        max-height="1000"        
      >

      <!-- -->
      <v-progress-linear
          color="black"
          indeterminate
          v-if="loading"
         ></v-progress-linear>
     
    <!---->
   <router-link to="/login"> <v-btn icon="mdi-arrow-left" size="32"  class="btn-back" :disabled="loading"></v-btn></router-link>

        <!---->
            <div class="text-subtitle-1 text-medium-emphasis">Email</div>
           
           <v-row>

                <v-col cols="7">
                  <v-text-field 
                    :rules="emailRules"
                      density="compact"
                      placeholder="Email"
                      prepend-inner-icon="mdi-email-outline"
                      variant="outlined"
                      v-model="email"
                      :readonly="isEmailReadonly"
                      required
                      :error-messages="emailErrors"
                      :disabled="loading"
                  ></v-text-field>
              </v-col>

                <v-col cols="5">
                
                    <v-btn 
                        v-show="!resend"
                        :key="`subscribe-${isSubscriber}`"
                        :border="`thin ${isSubscriber ? 'error' : 'success'}`"
                        :color="isSubscriber ? 'error' : 'success'"
                        :prepend-icon="isSubscriber ? 'mdi-close' : 'mdi-email'"
                        :slim="isSubscriber"
                        :text="isSubscriber ? 'Cancel' : ' '"
                        :variant="isSubscriber ? 'plain' : 'tonal'"
                        class="me-2 text-none" 
                        flat
                        @click="emailAuth()"
                        :disabled="loading"
                      ></v-btn>

                      <v-btn 
                        v-show="resend"
                        :key="`subscribe-${isSubscriber}`"
                        :border="`thin ${isSubscriber ? 'error' : 'success'}`"
                        :color="isSubscriber ? 'error' : 'success'"
                        :prepend-icon="isSubscriber ? 'mdi-close' : 'mdi-email'"
                        :slim="isSubscriber"
                        :text="isSubscriber ? 'Cancel' : '  '"
                        :variant="isSubscriber ? 'plain' : 'tonal'"
                        class="me-2 text-none"
                        :disabled="loading"
                        flat
                      @click="emailAuth()"
                      ></v-btn>
                </v-col>

         </v-row>
  

    <div class="text-subtitle-1 text-medium-emphasis" v-show="showAuthCode"></div>
           <v-row v-show="showAuthCode">
              <!---->
                <v-col cols="4">
                  <v-text-field 
                    density="compact"
                    placeholder=""
                    variant="outlined"
                    v-model="code"
                    :rules="codeRules"
                    required
                    :error-messages="codeErrors"   
                    :disabled="loading"                
                  ></v-text-field>
               </v-col>

               <v-col cols="2">
                  <v-btn
                        height="40"
                        min-width="50"
                        color="blue"
                        @click="emailValidation()"
                        :disabled="loading"
                    >
                    
                  </v-btn>
               </v-col>

               <!---->
              <v-col cols="6" class="mt-4">
                <small class="text-caption text-medium-emphasis">
                  <Timer ref="emailTimer"></Timer>
                </small>
              </v-col>
      
    
           </v-row>

        <div class="text-subtitle-1 text-medium-emphasis d-flex align-center ">
           <span class="text-caption ms-2 text-medium-emphasis">
                (      .)
                </span>
        </div>
        
        <v-row>
          <!---->
            <v-col cols="7">
                <v-text-field               
                  density="compact"
                  placeholder=""
                  prepend-inner-icon="mdi-account"
                  variant="outlined"
                  required
                  v-model="nickName"
                  :rules="nickNameRules"
                  :error-messages="nickNameErrors"
                  :readonly="isNickNameReadonly"
                  :disabled="loading"
                ></v-text-field>
            </v-col>

         <!-- -->
           <v-col cols="4">
                    <v-btn
                      v-show="!isNickNameClear"
                      height="40"
                      min-width="50"
                      color="blue"
                      :disabled="loading"
                      @click="nickNameValidation()"
                    >
                    
                    </v-btn>
             </v-col>

      </v-row>  

        <div class="text-subtitle-1 text-medium-emphasis d-flex align-center justify-space-between">
          
        </div>
          <!---->
        <v-row>
            <v-col cols="7">
                <v-text-field
                  density="compact"
                  placeholder=""
                  prepend-inner-icon="mdi-account"
                  variant="outlined"              
                  required
                  v-model="name"
                  :rules="nameRules"
                  :error-messages="nameErrors"     
                  :disabled="loading"          
                ></v-text-field>
           </v-col>
         </v-row>

   
       
            <div class="text-subtitle-1 text-medium-emphasis d-flex align-center justify-space-between">
            
            </div>
            <!---->
              <v-row>
                  <v-col cols="7">
                    <v-text-field
                      :append-inner-icon="visible ? 'mdi-eye-off' : 'mdi-eye'"
                      :type="visible ? 'text' : 'password'"
                      density="compact"
                      placeholder=""
                      prepend-inner-icon="mdi-lock-outline"
                      variant="outlined"
                      @click:append-inner="visible = !visible"
                      v-model="password"
                      :rules="passwordRules"
                      required
                      :error-messages="passwordErrors"
                      :disabled="loading"
                    ></v-text-field>
                  </v-col>
              </v-row>
   
        <div class="text-subtitle-1 text-medium-emphasis d-flex align-center justify-space-between">
         
        </div>
        <!-- -->
        <v-row>
            <v-col cols="7">
              <v-text-field
              :append-inner-icon="visible ? 'mdi-eye-off' : 'mdi-eye'"
              :type="visible ? 'text' : 'password'"
              density="compact"
              placeholder=""
              prepend-inner-icon="mdi-lock-outline"
              variant="outlined"
              @click:append-inner="visible = !visible"
              v-model="PasswordValidation"
              :rules="PasswordValidationRules"
              required
              :error-messages="PasswordValidationErrors"
              :disabled="loading"
              ></v-text-field>
             </v-col>
         </v-row>

        <div class="text-subtitle-1 text-medium-emphasis d-flex align-center justify-space-between">
          
        </div>

        <v-row>
            <v-col cols="3">
                <v-select
                  density="compact"               
                  variant="outlined"
                  v-model="firstPhoneNum"
                  :items="items"                          
                  required
                  :disabled="loading"
                ></v-select>
            </v-col>


            <v-col cols="3">
                  <v-text-field                 
                        type="text"
                        density="compact"
                        variant="outlined"
                        v-model="middlePhoneNum"                      
                        required                
                        maxlength="4" 
                        :disabled="loading"                   
                  ></v-text-field>

            </v-col>

            <v-col cols="3">
                <v-text-field        
                  type="text"
                  density="compact"
                  variant="outlined"
                  v-model="lastPhoneNum"         
                  required        
                  maxlength="4"   
                  :disabled="loading"
                ></v-text-field>
            </v-col>

    </v-row>

        <v-row>

              <v-textarea
              label="   ."
              row-height="30"
              rows="4"
              variant="filled"
              auto-grow
              shaped
              v-model="introduce"
              maxlength="100" 
              :disabled="loading"
            ></v-textarea>
        </v-row>

    <!-- -->
        <v-btn
          class="mb-8"
          color="blue"
          size="large"
          variant="tonal"
          block
          @click="submitJoin()"
          :disabled="loading"
        >
         
        </v-btn>
        
     
      </v-card>


     
    </div>
    <!-- ()  -->
    <FindIdComponent v-model:value="findIdDialog" @findIdClose=closeFindIdDiolog />
    <!--    -->
    <FindPwdComponent v-model:value="findPwdDialog" @findPwdClose=closeFindPwdDiolog />
  </template>



  <script>
   import api from "@/api"
   import Timer from "@/components/common/utill/Timer.vue"
    export default {
    data(){
        return{
              visible: false, //   or 
              items:['010','011','016','017','019'], //  
              firstPhoneNum:'010', // - >  
              middlePhoneNum:'',// 
              lastPhoneNum:'', //  

              password:'',// 
              PasswordValidation:'',//   
              email:'', //  
              code:'',//   
              nickName:'',//  
              name:'',// 
              introduce:'',//
              loading: false, //   = >       .

              showAuthCode:false, //         
              isEmailReadonly:false, //        
              isNickNameReadonly:false,//       

              isEmailAuthClear:false ,//  
              isNickNameClear:false, //  
              resend:false, //   
              
              //
              emailRules: [
                v => !!v || '  .', //   
                v => v.trim().length > 0 || '   .', //   
                v => !/\s/.test(v) || '   .', //  
                v => /.+@.+\..+/.test(v) || '   .' //@ .   
              ],
              passwordRules: [
                v => !!v || '  .', //   
                v => v.trim().length > 0 || ' .', //  
                v => !/\s/.test(v) || '   .', //  
                v => (v && v.length >= 8) || '  8  .', //  8
                v => (v && v.length <= 20) || '  20   .', //  20
                v => /[a-zA-Z]/.test(v) || '   ()  .', //  
                v => /\d/.test(v) || '     .', //  
                v => /[!@#$%^&*(),.?":{}|<>]/.test(v) || '     .', //  
              ],
              PasswordValidationRules:[
              v => !!v || '   .', //      
              v => v.trim().length > 0 || ' .', //  
              v => !/\s/.test(v) || '   .', //  
              v => v === this.password || '  .', //       
              ],
              codeRules:[
                v => v.trim().length > 0 || '  ',// 
               v => !!v || '  .', //null  
               v => !/\s/.test(v) || '   .', //  
              ],
              nickNameRules:[
              v => !!v || '  .', //    
              v => v.trim().length > 0 || '    .', //   
              v => !/\s/.test(v) || '   .', //  
              v => !/[-|-|-]/.test(v) || '    .', //   
              v => /^[a-zA-Z0-9_ ]+$/.test(v) || ' _     .', //  
              v => v.length <= 15 || '  15   .', //   
            
              ],
              nameRules:[
              v => !!v || '  .', //    
              v => v.trim().length > 0 || '    .', //   
              v => !/\s/.test(v) || '   .', //  
              ],

              //     = >     .
              nameErrors:[],//  
              nickNameErrors:[],// 
              codeErrors:[],//
              emailErrors: [],//  
              passwordErrors: [],//  
              PasswordValidationErrors:[],//   
              
          }
         
      },
      computed:{
        fullPhoneNumber() {
        //        
        return `${this.firstPhoneNum}-${this.middlePhoneNum}-${this.lastPhoneNum}`;
        }
      },
      components:{
        Timer
      },
      methods:{
        validateField(value, rules) { //rules   
          return rules
            .map(rule => rule(value))
            .filter(error => typeof error === 'string');
        },
        //       
        showAlert(message) { 
          return new Promise((resolve) => {
            alert(message);
            resolve();
          });
        },
      
        validatePhoneNumber() {
          //    
          this.phoneErrors = this.validateField(this.fullPhoneNumber, this.phoneRules);
        },
        AuthCodeOpen(){ //   
          this.showAuthCode=true
        },
        AuthCodeClose(){
          this.showAuthCode=false
        },
        //    
        emailAuth(){
          if(this.loading===true){
            return
          }

          this.emailErrors = this.validateField(this.email, this.emailRules); //   

          if (this.emailErrors.length > 0 ) { //       return
            
            return;// axios    
          }

         
         
         
                  this.loading = true; //   = >  
                  //   API
                    api.post('/members/emailAuth',{
            
                      email:this.email
                    })
                    .then(()=>{
                      //              
                      this.AuthCodeOpen()//   
                      this.isEmailReadonly=true//    
                      this.resend=true//  
                      
                      this.$refs.emailTimer.resetTimer();//  
                    
                      alert('  .')
                      
                    })
                    .catch(err => {
                      //     
                      if(err?.response?.data?.message){
                      alert(err?.response?.data?.message)  
                      }
                    })
                    .finally(() => {
                      this.loading = false; //  
                    });
              
                
               
         
        },
        //   
        emailValidation(){
          if(this.isLoading===true){
            return
          }

          this.codeErrors = this.validateField(this.code, this.codeRules); //   
          if (this.codeErrors.length > 0 ) { //       return
            
            return;// axios    
          }
          //  FALSE
          this.isLoading=true

          //  API
          api.post('/members/emailValidate',{
            'email':this.email,// 
            'code':this.code// 
          })
          .then(()=>{
            //      
            this.AuthCodeClose();//   
            this.isEmailAuthClear=true; // 
            alert('.')
          })
          .catch((err)=>{
            //
            if(err?.response?.data?.message){
            alert(err?.response?.data?.message)  
            }
       
         })
         .finally(()=>{
          this.isLoading=false//  FALSE 
         })
         
        },
        //   
        nickNameValidation(){
          if(this.isLoading===true){
            return
          }

          //   
          this.nickNameErrors = this.validateField(this.nickName, this.nickNameRules); 

                 //       return
                if (this.nickNameErrors.length > 0 ) {
          
                return;// axios   
         
                }
                
                // true=  http
                this.isLoading=true
                
                //  api  
                api.post('/members/nickValidate',{
                  nickname:this.nickName//body  
                })
                .then(()=>{
                  // 
                    alert(' .')
                    this.isNickNameClear =true; //   
                    this.isNickNameReadonly=true;//    
                  
                })
                .catch((err)=>{
                    //
                    if(err?.response?.data?.message){
                    alert(err?.response?.data?.message)  
                    }
                    
                })
                .finally(()=>{
                  //  false 
                  this.isLoading=false
                })
              
            
        },
        //    ( ,    )
        submitJoin(){
          //  true  
          if(this.isLoading===true){
            return
          }
          
          //    
          if(!this.isEmailAuthClear){ 
            alert('  .')
            return
          }
          //   
          if(!this.isNickNameClear){ 
            alert('  .')
            return
          }
          //
          this.nameErrors = this.validateField(this.name, this.nameRules); 
          //
          this.passwordErrors=this.validateField(this.password,this.passwordRules);
          // 
          this.PasswordValidationErrors=this.validateField(this.PasswordValidation,this.PasswordValidationRules); 

                //   
                if (this.nameErrors.length > 0 || this.passwordErrors.length>0 ||this.PasswordValidationErrors.length>0 ) {  
        
                return;// axios    
                }

                //   (010 +  +  )
               const fullPhone=(this.firstPhoneNum+this.middlePhoneNum+this.lastPhoneNum); 
               
               //   validation
               if (fullPhone.length !== 11) {
                  alert('  11 .');
                  return;
                }

                //    
                if (/[-]/.test(fullPhone)) {
                  alert('     .');
                  return;
                }

                //    
                if (/\s/.test(fullPhone)) {
                  alert('     .');
                  return;
                }

                //      
                if (!/^\d+$/.test(fullPhone)) {
                  alert('    .');
                  return;
                }

               //  
                const formdata=new FormData();
                formdata.append('email',this.email)//
                formdata.append('password',this.password)//
                formdata.append('nickname',this.nickName)//
                formdata.append('name',this.name)//
                formdata.append('phone',fullPhone)//
                formdata.append('introduce',this.introduce)//( )

                // 
                this.isLoading=true

                // api  
                api.post("/members", formdata) 
                .then(
                  async () => {//async / await  
                 
                    // showAlert  alert     
                    await this.showAlert(' . !!!');
                    // alert   router.push 
                    this.$router.push('/login');
                  
                })
                .catch((err)=>{
                  //
                  if(err?.response?.data?.message){
                  alert(err?.response?.data?.message)  
                  }
                  
                })
                .finally(()=>{
                  // false
                  this.isLoading=false
                })
               
                
        }
      }
    }
  </script>

  <style>

.btn-back {
  position: absolute;
  top:8px; 
  left: 10px; 
}

.card-scroll {
  overflow-y: auto; 
  max-height: 500px; 
}

  </style>