<template>
    <div class="text-center">
      
      <v-dialog
        v-model="localDialog"
        max-width="600"
      >
       
        <v-progress-linear
          color="cyan"
          indeterminate
          v-if="isLoading"
         ></v-progress-linear>
        <v-card
          prepend-icon="mdi-account"
          title="  "
          class="to-blackMode"
        >
          <v-divider></v-divider>
            <v-card-text>

          



            <div class="text-subtitle-1 text-medium-emphasis d-flex align-center justify-space-between">
              <span style="color: aliceblue;"> </span>
            </div>

        <v-row>

                <v-col cols="3">
                    <v-select
                      density="compact"           
                      variant="outlined"
                      v-model="firstPhoneNum"
                      :items="items"            
                      required
                      :disabled="isLoading"
                    ></v-select>
                </v-col>


                <v-col cols="3">
                      <v-text-field
                        type="text"
                        density="compact"
                        v-model="middlePhoneNum"
                        maxlength="4" 
                        variant="outlined"
                        :disabled="isLoading"
                      ></v-text-field>

                </v-col>

                <v-col cols="3">

                    <v-text-field
                      type="text"
                      density="compact"
                      maxlength="4" 
                      variant="outlined"
                      v-model="lastPhoneNum"
                      :disabled="isLoading"
                    ></v-text-field>

                </v-col>

         </v-row>

             <div class="text-subtitle-1 text-medium-emphasis d-flex align-center justify-space-between">
              <span style="color: aliceblue;"></span>
            </div>


        <!---->
        <v-row>
            <v-col cols="12">
              <v-text-field
                :append-inner-icon="visible ? 'mdi-eye-off' : 'mdi-eye'"
                :type="visible ? 'text' : 'password'"
                density="compact"
                placeholder=""
                prepend-inner-icon="mdi-lock-outline"
                variant="outlined"
                @click:append-inner="visible = !visible"
                v-model="password"     
                :rules="passwordRules"
                required
                :error-messages="passwordErrors"
                :disabled="isLoading"
              ></v-text-field>
            </v-col>
        </v-row>
   
    
    </v-card-text>
  
          <v-divider></v-divider>
  
          <v-card-actions>
            <v-spacer></v-spacer>
  
            <v-btn
              text="Close"
              variant="plain"
              @click="closeDialog()"
              :disabled="isLoading"
            ></v-btn>
  
            <v-btn
              color="primary"
              text="Save"
              variant="tonal"
              @click="submitChangePhone()"
              :disabled="isLoading"
            ></v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </div>
  </template>

  <script>
import api from '@/api';

export default {
    name: 'ChangerPhone',
  

    props: {
      value: {//      
        type: Boolean,
        required: true
      }
    
    },
    data(){

    return{
        password:'',
        passwordRules: [
                v => !!v || '  .', //   
                v => v.trim().length > 0 || ' .', //  
                v => !/\s/.test(v) || '   .', //  
        
        ],
        passwordErrors:[],
        visible:false,
        items:['010','011','016','017','019'], //  
        firstPhoneNum:'010', // - >  
        middlePhoneNum:'',// 
        lastPhoneNum:'', //  
        isLoading:false//   
    }
  },
  computed:{
    //     (true/false)  , props value   .
      localDialog:{
          get(){
              return this.value
          }
      }
  },   
    
    methods: {
      //rules   
      validateField(value, rules) { 
          return rules
            .map(rule => rule(value))
            .filter(error => typeof error === 'string');
        },

      //  
      closeDialog() {

        // 
        this.password='',//
        this.firstPhoneNum='',//  
        this.middlePhoneNum='',//
        this.lastPhoneNum='',// 
        this.passwordErrors=[]//  

        this.$emit('changePhone');//     
      },
      //    
      submitChangePhone(){
              //  return 
              if(this.isLoading===true){
                  return
              }
              //   
              this.passwordErrors = this.validateField(this.password, this.passwordRules); 

              //       return
              if (this.passwordErrors.length > 0 ) { 
              
                return;// axios   
    
              }
              //    
              const fullPhone=(this.firstPhoneNum+this.middlePhoneNum+this.lastPhoneNum); 
            

              //    validation
               if (fullPhone.length !== 11) {
                  alert('  11 .');
                  return;
                }

                //    
                if (/[-]/.test(fullPhone)) {
                  alert('     .');
                  return;
                }

                //    
                if (/\s/.test(fullPhone)) {
                  alert('     .');
                  return;
                }

                //      
                if (!/^\d+$/.test(fullPhone)) {
                  alert('    .');
                  return;
                }

                //   
                this.isLoading=true; 


                //   api 
             api.put('/setting/phoneChange',{
                phone:fullPhone, //  
                password:this.password //  
             })
             .then(()=>{// 
                alert('    .')
                this.closeDialog()//  
            })
            .catch((err)=>{ //    
       
                     if(err?.response?.data?.message){
                        alert(err?.response?.data?.message)  
                    }
              
            })
            .finally(()=>{
              this.isLoading=false; // 
            })
            
      }

    }
  }
  </script>