<template>
   
    <v-dialog activator="parent" v-model="localDialog" max-width="500">
              <template v-slot:default="{ isActive }">

                <v-progress-linear
                  color="cyan"
                  indeterminate
                  v-if="isLoading"
                ></v-progress-linear>

                <v-card rounded="lg" class="to-blackMode">
                  <v-card-title class="d-flex justify-space-between align-center">

                    <div class="text-h5 text-medium-emphasis ps-2">
                     <span style="color: white;">   </span>
                    </div>
    
                    <v-btn  
                      variant="text"
                      @click="isActive.value = false"
                      :disabled="isLoading"
                    ></v-btn>

                  </v-card-title>
    
                  <v-divider class="mb-4"></v-divider>
    
                  <v-card-text>
                    <v-file-input
                      label="  "
                      prepend-icon="mdi-camera"
                      variant="filled"
                      accept="image/*"
                      @change="handleFileEvent"
                      :disabled="isLoading"
                    ></v-file-input> 
    
                   
                        <div class="info-text">
                                     .
                        </div>
                                
                   
                  </v-card-text>
    
                  <v-divider class="mt-2"></v-divider>
    
                  <v-card-actions class="my-2 d-flex justify-end">
                    <v-btn
                      class="text-none"
                      rounded="xl"
                      text="Cancel"
                      @click="closeDialog()"
                       :disabled="isLoading"
                    ></v-btn>
    
                    <v-btn
                      class="text-none"
                      color="primary"
                      rounded="xl"
                      text="Send"
                      variant="flat"
                      @click="changeProfile()"
                      :disabled="isLoading"
                    ></v-btn>
                  </v-card-actions>
                </v-card>
              </template>
            </v-dialog>
    
    </template>
    
    <script>
import api from '@/api';

    export default {
      name: 'ChangeProfileImg',
    
    
      props: {
        value: {//     
          type: Boolean,
          required: true
        }
      
    },data(){
      return{
        profileImgFile:null, //  
        isLoading:false     
      }
    }
    ,computed:{
      localDialog:{
          get(){
              return this.value //     (true/false)  , props value   .
          }
      }
    }   
      ,
      methods: {
        //     
        closeDialog() {
          this.profileImgFile=null,
          this.isLoading=false
          this.$emit('changeProfileImgClose');
        },
        //   
         handleFileEvent(event){
          const file= event.target.files[0]
          if(!file)return;
          //    
          const MAX_SIZE_MB = 5;
          const MAX_SIZE_BYTES = MAX_SIZE_MB * 1024 * 1024; //    ()
    
          //  5mb    
          if (file.size > MAX_SIZE_BYTES) {
            alert(` '${file.name}'  ${MAX_SIZE_MB}MB .`);
            return;
          }
    
           //  data  
           this.profileImgFile = file;
    
       },
       //  API 
       changeProfile(){
        //  
        if(this.isLoading){
        return
        }
        //  FORMDATA  
       const formData= new FormData()
       formData.append('profileImgFile',this.profileImgFile)
       // 
       this.isLoading=true;
       //API  
       api.put('/feed/userfeed',formData)
       .then(()=>{
           alert('   .')
           this.closeDialog()
       })
       .catch((err)=>{
        if(err?.response?.data?.message){
        alert(err?.response?.data?.message)  
        }
       })
       .finally(()=>{
        this.isLoading=false
       })
       } 
      }
    }
    </script>
    
 <style>
.info-text {
  color: grey; 
  font-size: 0.875rem; 
  margin-top: 8px; 
  text-align: center;
}
 </style>