<template>
    
  <v-container>
   
        <v-progress-linear
          color="cyan"
          indeterminate
          v-if="isLoading"
         ></v-progress-linear>
  
      <v-row v-if="!postData.length&&!isLoading">
        <v-col class="d-flex child-flex justify-center align-center" cols="12">
                .
        </v-col>
      </v-row>

    <v-row v-if="postData.length">
      <!-- -->
      <v-col
        v-for="(post,index) in postData" :key="index"
        class="d-flex child-flex"
        cols="4"
      
      >
        <v-img 
          :lazy-src="`https://picsum.photos/10/6?image=${n * 5 + 10}`"
          :src="post.postImgUrl"
          aspect-ratio="1"
          class="image-container"
          cover
          @click="openPostDetailDialog(post.postNum)"
        >
          <template v-slot:placeholder>
            <v-row
              align="center"
              class="fill-height ma-0"
              justify="center"
            >
              <v-progress-circular
                color="grey-lighten-5"
                indeterminate
              ></v-progress-circular>
            </v-row>
          </template>
          
          <div class="overlay">
            <v-icon class="overlay-icon">mdi-heart</v-icon>
            {{ post.likeCount }}
          </div>
        </v-img>
      </v-col>
      <!-- -->
    </v-row>

  <div ref="sentinel" class="sentinel" ></div>

  <PostDetail 
   :value="postDetailDialog"
   :postNum="sendPostNum"
   @postDetailClose="closePostDetailDialog"
   ></PostDetail>
  
  </v-container>
  


</template>

<script>
import api from '@/api';
import PostDetail from '../post/PostDetail.vue';
export default {
  data() {
    return {
      postDetailDialog:false, //   
      page:1, // 
      postData:[], //    
      isLoading:false, //     
      observer:null, //intersection observer 
      noMoreNeedData:false, //     api     
      firstCall:false ,//     x   
      sendPostNum:0//    
    };
    
  },
   // intersection observer   ref
   computed:{
    sentinel() {
      return this.$refs.sentinel;
    }
  },
  components:{
    PostDetail
  },
  // 
  mounted(){
    this.initObserver(); // IntersectionObserver 
    this.callMainPostList()//1   
  },
   //     
   beforeUnmount() {
    if (this.observer) {
      this.observer.disconnect(); 
    }
  },
  methods:{
    //  
    openPostDetailDialog(postNum){
      this.sendPostNum=postNum
      this.postDetailDialog=true;
    },
    //  
    closePostDetailDialog(){
      this.sendPostNum=0;
      this.postDetailDialog=false;
    },
    //     
    callMainPostList(){
      //  ,      
      if(this.isLoading || this.noMoreNeedData)return
      //   
      this.isLoading=true
      //    x   
      if(this.firstCall){
        this.page++;// 
      }
      //        
      this.firstCall=true
      //   api
     api.get(`/feed/globalfeed/${this.page}`) 
     .then((res)=>{
        //    
        const resData=res?.data?.reqData
        //   
        if(resData.length){
          //   
          this.postData=[...this.postData,...resData]
        }
        else{
          //           
          this.noMoreNeedData=true;
          this.page--;
        }

     })
     .catch((err)=>{
      if(err?.response?.data?.message){
        alert(err?.response?.data?.message)  
        }
     })
     .finally(()=>{
      //   
      this.isLoading=false
     })
    },
     //intersectionobserver  
     initObserver() {
      this.$nextTick(() => {
       //ref sentinel 
        const sentinel = this.$refs.sentinel;
  
        if (sentinel) {
          this.observer = new IntersectionObserver((entries) => {
            //
            entries.forEach(entry => {
              if (entry.isIntersecting) {
               
                this.callMainPostList() // Sentinel     
              }
            });
          }, {
            root:null, // viewport root 
            rootMargin: '100px',
            threshold: 0.1 
          });
          this.observer.observe(sentinel); // Sentinel  
        }
      });
    }
  }
}
</script>

<style scoped>


.image-container {
  position: relative;
  overflow: hidden;
  transition: transform 0.3s ease;
}

.image-container:hover {
  transform: scale(1.05);
  cursor: pointer; 
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5); 
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.image-container:hover .overlay {
  opacity: 1;
}

.overlay-icon {
  font-size: 48px; 
  color: white;
  margin: 0 10px;
  cursor: default;
}


</style>
