<template>
    <div class="text-center ">
      <v-dialog
        v-model="localDialog"
        transition="dialog-bottom-transition"
        fullscreen   
      > 

        <v-card  class="to-blackMode">

          <!--closeDialog = >   -->
          <v-toolbar  style="background-color: gray; color: aliceblue;">

              <v-btn
                icon="mdi-close"
                @click="closeDialog()"
              ></v-btn>

              <v-toolbar-title >Settings</v-toolbar-title>
         
          </v-toolbar>
  
          <v-list
            lines="two"
            subheader
             class="to-blackMode"
          >
            <v-list-subheader  class="to-blackMode" style="opacity: 0.7;"></v-list-subheader>
         
            <!--        true -->
                  <v-list-item
                    subtitle="change nickname"
                    title=""
                    link
                    @click="openChangeNickname"
                  ></v-list-item>
        
                  <v-list-item
                    subtitle="change password"
                    title=" "
                    link
                    @click="openChangePwd"
                  ></v-list-item>
        
                  <v-list-item
                    subtitle="change phone number"
                    title="  "
                    link
                    @click="openPhoneChange"
                  ></v-list-item>
                  <v-divider></v-divider>
                  <v-list-subheader  class="to-blackMode" style="opacity: 0.7;"></v-list-subheader>
                
                  <v-list-item
                  v-if="memberData.locked==='PUBLICID'"
                    :key="updateKey"
                    subtitle="transfer "
                    title=" "
                    link
                    @click="openAlert(
                      '       ?',
                    'ChangeLokedState')"
                  ></v-list-item>

                  <v-list-item
                  v-if="memberData.locked==='LOCKED'"
                    subtitle="transfer "
                    title=" "
                    link
                    @click="openAlert(
                      '    ',
                      'ChangeLokedState'
                    )"
                  ></v-list-item>
        
                  <v-list-item
                    subtitle="change password"
                    title=""
                    class="red-text"
                    link
                    @click="openAlert(
                      '    ,    . ?',
                      'memberDelete'
                      )"
                  ></v-list-item>

            <v-divider></v-divider>


      
          </v-list>
        </v-card>
      </v-dialog>
    </div>
    <!--   -->
    <Alert
      v-model:value="isAlertOpen"
      v-model:altype="alertType"
      v-model:message="alertMessage"
      @closeAlertDialog="closeAlert"
    >
  </Alert>
    <!--   -->
    <DeleteVerify v-model:value="deleteVerifyDialog" @DeleteVerifyClose="closeDeleteVerifyDialog"></DeleteVerify>
    <!--   -->
    <ChangePwd v-model:value="changePwdDialog" @changePwdClose="closeChangePwd"></ChangePwd>
    <!--  -->
    <ChangePhone v-model:value="phoneChangeDialog" @changePhone="closePhoneChange"></ChangePhone>
    <!--  -->
    <ChangeNickname v-model:value="nicknameChangeDialog" 
    @changeNickname="closeChangeNickname"></ChangeNickname>
  </template>

<script>


import ChangePwd from "./ChangePwd.vue"
import ChangePhone from "./ChangePhone.vue"
import ChangeNickname from "./ChangeNickName.vue"
import DeleteVerify from "./DeleteVerify.vue"
import Alert from "../utill/Alert.vue"
import api from "@/api"
export default {
    name:'SettingComponent',
    props: {
       value: {//     
         type: Boolean,
         required: true
       },
       memberData :{//
        type:{},
        required:true
       }
      
      },
  data () {
    return {
     
      notifications: false, 
      sound: true,
      widgets: false,
      changePwdDialog:false, //  
      phoneChangeDialog:false, //    
      nicknameChangeDialog:false, // 
      deleteVerifyDialog:false,//  
      alertType:'', //    (  ? ?)
      isAlertOpen:false,//   
      alertMessage:'' ,//   
    
   
    }
  },
  computed:{
    //     (true/false)  , props value   .
     localDialog:{
         get(){
       
             return this.value 
         }
     },
     currentLockState:{
      get(){ //   /   
        return this.memberData.locked
      }
      
     }
  },
     methods: {
       //       
       closeDialog() {
         this.$emit('settingClose');
       },
       //       
       openChangePwd(){
        this.changePwdDialog=true 
       },
       //       
       closeChangePwd(){
        this.changePwdDialog=false 
       },
       //   
       openPhoneChange(){
        this.phoneChangeDialog=true 
       },
       //   
       closePhoneChange(){
        this.phoneChangeDialog=false
       },
       //   
       openChangeNickname(){ 
          this.nicknameChangeDialog=true
       },
       //   
       closeChangeNickname(){ 
          this.nicknameChangeDialog=false
       },
       //           
       //   ,  ( ,   )   
       openAlert(message,type){

        this.alertMessage=message //    , 
        this.alertType=type//   ,  

        this.isAlertOpen=true //   
       },
       //   ( no ok    . )
       closeAlert(isOk,type){//ok or no  ,    (,  )
        
        //ok  ,  /     (type )       
        if (isOk===true&&type==='ChangeLokedState') {
          const curstate= this.currentLockState //   /    curstate 
          ///   api  
          api.put('/setting/lockStateChange',{
            locked:curstate // / 
          })
          .then((res)=>{
            //   
            alert('.')
            const locked=res?.data?.reqData?.locked
            this.$emit("updateSideMenuInfo",locked)// locked      
              //res.data.reqData.locked       = >    
          })
          .catch((err)=>{
            //   data  message   
            if(err?.response?.data?.message){
              alert(err?.response?.data?.message)  
              }
          })
        }

        //ok  ,    
        if(isOk&&type==="memberDelete"){
          this.deleteVerifyDialog=true//    
          this.isAlertOpen=false//  

        }

        this.isAlertOpen=false// no      
       },
       //     
       closeDeleteVerifyDialog(){
        this.deleteVerifyDialog=false//  
       }

     },
     components:{
      ChangePwd ,//  
      ChangePhone, //  
      ChangeNickname, //  
      Alert ,//,
      DeleteVerify//  
     }
  

}
</script>
<style>
.red-text{
  color:#ff6f6f ;
}
</style>