<template>
  <div
    class="autocomplete-container"
    ref="container"
    @keydown.arrow-down="handleArrowDown"
    @keydown.arrow-up="handleArrowUp"
    @keydown.enter="handleEnter"
    tabindex="0"
    style="border-radius: 0px 0px 8px 8px;"
  >
    <ul v-show="results.length" class="results-list">
      <li
        v-for="(result, index) in results"
        :key="index"
        class="result-item"
        :class="{ selected: index === selectedIndex }"
        @click.stop="handleClick(result.hobby)"
        @mouseover="handleMouseOver(index)"
        :ref="index === selectedIndex ? 'selectedItem' : null"
      >
       # {{ result.hobby }}
      </li>
      <li ref="sentinel" class="result-item sentinel"></li>
    </ul>
    <div v-if="isFetching" class="loading-indicator">
      <v-progress-circular indeterminate color="primary" size="30"></v-progress-circular>
    </div>
  </div>
</template>

<script>
import api from "@/api";
import debounce from 'lodash/debounce';

export default {

  name:'HobbySearchResult',
  props: {
    keyword: String,
    isHashtag: Boolean,
  },
  data() {
    return {
      results: [], //    
      selectedIndex: -1, //   
      previousIndex: -1, //   
      isFetching: false, //    
      page: 1, //  
      observer: null, // IntersectionObserver 
      firstCall:true,//      
      isNomoreData:false,//     .
      previousKeyword:'' // 
    };
  },
 
  computed: {
    container() {
      return this.$refs.container;
    },
    sentinel() {
      return this.$refs.sentinel;
    },
  },
  watch: {
    keyword: {
      
      handler(newKeyword) {
      
      
        if (newKeyword && this.isHashtag && newKeyword!==this.previousKeyword) {
          
          this.page = 1; //  1 
          this.results = []; //   
          this.selectedIndex = -1; //   
          this.previousIndex = -1; //   
          this.isNomoreData = false;//     
          this.firstCall=true//      
          this.debouncedFetchResults(newKeyword); //  
        } else {
          this.results = []; //   
          this.selectedIndex = -1; //   
          this.previousIndex = -1; //   
          this.firstCall=true //      
        }
        this.previousKeyword=this.keyword //  
      },
      immediate: true, //    
    },
  },
  methods: {
    async fetchResults(keyword) {
      const sendKeyword = keyword.substring(1); // '@' 
      if (sendKeyword.includes('#') ) return; //    

         //  : `_`, , ,  
         const forbiddenChars = /[^a-zA-Z0-9_---]/;
         //validation
          if (forbiddenChars.test(sendKeyword)) {
            return
          }
          //     #   ,    
          if (this.isFetching || !this.isHashtag || this.isNomoreData) {
          
            return
          } 
    
       this.isFetching = true; //   

       //           
       if(!this.firstCall){
        this.page += 1;
       }
       //  ,      
       this.firstCall=false;
     
      
      //NULL  X 
      if(sendKeyword===''||sendKeyword===null){
        this.isFetching=false;
        return
      }
      //api  
     
       api.get(`/hobby/${this.page}`,{
        params:{keyword:sendKeyword}
       })
       .then((res)=>{
        // 
        const newHobbies = res?.data?.reqData?.findList;
        //     
        if (newHobbies?.length) {
          this.results = [...this.results, ...newHobbies];
        
        } else {
          //     ,    true
          this.page--;
          this.isNomoreData = true;
        }
       })
       .catch((err)=>{
        if(err?.response?.data?.message){
        alert(err?.response?.data?.message)  
        }
       })
       .finally(()=>{
       //   
        this.isFetching = false;
       })
       
     
    },//
    debouncedFetchResults: debounce(function (keyword ) {
      this.fetchResults(keyword);//    fetchResults 
    }, 300),
    // intersection observer   
    handleIntersection(entries) {
      entries.forEach((entry) => {
        if (entry.isIntersecting && entry.target === this.sentinel) { // Sentinel  
          if (!this.isFetching && !this.isNomoreData) {
            
            this.debouncedFetchResults(this.keyword); //  
          }
        }
      });
    },
    //     
    handleArrowDown() {
      if (this.results.length === 0) return; //      

      this.previousIndex = this.selectedIndex; //   
      if (this.selectedIndex === -1) {
        this.selectedIndex = 0; //       
      } else {
        this.selectedIndex = (this.selectedIndex + 1) % this.results.length; //   
      }
      this.scrollToSelectedItem(); //    
      this.checkAndLoadMore(); //     
    },
    //    
    handleArrowUp() {
      if (this.results.length === 0) return; //      

      this.previousIndex = this.selectedIndex; //   

      if (this.selectedIndex === 0) {
        this.selectedIndex = this.results.length - 1; //         
      } else if (this.selectedIndex === -1) {
        this.selectedIndex = this.results.length - 1; //      
      } else {
        this.selectedIndex = (this.selectedIndex - 1 + this.results.length) % this.results.length; //   
      }
      this.scrollToSelectedItem(); //    
      this.checkAndLoadMore(); //     
    },
    //   
    handleEnter() {
      if (this.isHashtag) {
        if (this.selectedIndex >= 0 && this.selectedIndex < this.results.length) {
          this.handleClick(this.results[this.selectedIndex].hobby); //    
        } else {
          this.$emit('enterNoSearch');
      }
    }
  },
    //
    handleClick(hobby) {     
      this.$emit('selectHobby', hobby); //   
    },
    handleMouseOver(index) {    
      this.previousIndex = this.selectedIndex; //   
      this.selectedIndex = index; //   
      this.scrollToSelectedItem(); //    
    },
    //  
    scrollToSelectedItem() {
      const selectedItem = this.container?.querySelector('.selected'); //   
      if (selectedItem && this.container) {
        const itemHeight = selectedItem.offsetHeight; //  
        const containerHeight = this.container.clientHeight; //  
        const scrollTop = this.container.scrollTop; //   
        const selectedItemTop = selectedItem.offsetTop; //    

        if (this.selectedIndex === 0) { //    
          if (this.previousIndex !== -1 && this.previousIndex < this.selectedIndex) {
            const previousItem = this.container?.querySelectorAll('.result-item')[this.previousIndex]; //   
            if (previousItem) {
              this.container.scrollTop = previousItem.offsetTop; //    
            }
          } else {
            this.container.scrollTop = 0; //      
          }
        } else if (this.selectedIndex === this.results.length - 1) { //   
          this.container.scrollTop = this.container.scrollHeight; //   
        } else { //   
          if (selectedItemTop < scrollTop) {
            this.container.scrollTop = selectedItemTop - itemHeight * 2.5; // 2  
          } else if (selectedItemTop + itemHeight > scrollTop + containerHeight) {
            this.container.scrollTop = selectedItemTop + itemHeight * 2.5 - containerHeight; // 2  
          }
        }

        //      
        if (this.container.scrollTop < 0) {
          this.container.scrollTop = 0;
        }
        if (this.container.scrollTop > this.container.scrollHeight - containerHeight) {
          this.container.scrollTop = this.container.scrollHeight - containerHeight;
        }
      }
    },
    //    
    checkAndLoadMore() {
      const containerElement = this.container;
      const selectedItem = containerElement?.querySelector('.selected'); //   
      if (selectedItem) {
        const containerRect = containerElement.getBoundingClientRect(); //    
        const itemRect = selectedItem.getBoundingClientRect(); //     

        if (itemRect.bottom > containerRect.bottom - 50) { //      
          if (!this.isFetching) {
            
            this.debouncedFetchResults(this.keyword); //  
          }
        }

        if (itemRect.top < containerRect.top + 50) { //      
          if (!this.isFetching) {
           
            this.debouncedFetchResults(this.keyword); //  
          }
        }
      }
    },
    //observer  
    initObserver() {
      if (this.container) {
        this.$nextTick(() => {
          if (this.sentinel) {
            this.observer = new IntersectionObserver(this.handleIntersection, {
              root: this.container,
              rootMargin: '0px',
              threshold: 0.1,
            });
            this.observer.observe(this.sentinel);
          }
        });
      }
    },
    //        
    handleClickOutside(event) {
     
     if (this.$refs.container && !this.$refs.container.contains(event.target)) {
    
      this.$emit('closeSearch')
     }
    }
  },
  //  observer   ,    
  mounted() {
    document.addEventListener('click', this.handleClickOutside.bind(this)); // bind(this) 
    this.initObserver();
  },
  //   
  beforeUnmount() {
    document.removeEventListener('click', this.handleClickOutside.bind(this)); // bind(this) 
    if (this.observer) {

      this.observer.disconnect();
    }
  }
};
</script>

<style scoped>
.selected {
  background-color: #888;
}
.results-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  max-height: 200px;
}
.result-item {
  padding: 8px;
  cursor: pointer;
}
.result-item:hover {
  background-color:  #888;
}
.sentinel {
  height: 20px;
  background: transparent;
}
.loading-indicator {
  text-align: center;
  padding: 10px;
  color: #888;
}
</style>
