import { createApp } from 'vue'
import App from './App.vue'
import router from './router/router'
import vuetify from './plugins/vuetify'
import { loadFonts } from './plugins/webfontloader'
import Footer from './components/common/layout/Footer.vue'
import Header from './components/common/layout/Header.vue'
import Sidemenu from './components/common/layout/Sidemenu.vue'
import HobbySearchResult from './components/common/utill/HobbySearchResult.vue'
import GlobalSearchResult from './components/common/utill/GlobalSearchResult.vue'
import FollowSearchResult from './components/common/utill/FollowSearchResult.vue'
/*
   
1. axios   api       ,    unmount   , catch  err.response  undefined  
 ====>  catch  err.response        
 
 2.   (  ) = > 1      2           
     409    

 3. axios     response    410    api      
                .
            isRefreshing          
    .
*/



loadFonts();

createApp(App)
.component('Footer', Footer)
.component('Header', Header)
.component('Sidemenu', Sidemenu)
.component('HobbySearchResult',HobbySearchResult)
.component('FollowSearchResult',FollowSearchResult)
.component('GlobalSearchResult',GlobalSearchResult)
.use(router)
.use(vuetify)
.mount('#app');

