import { createRouter, createWebHistory } from 'vue-router'
import Login from "@/components/member/Login.vue"
import Join from '@/components/member/Join.vue'
import UserFeed from '@/components/feed/userfeeds/UserFeed.vue'
import globalfeed from '@/components/feed/otherfeeds/Globalfeeds.vue'
import Mainfeed from '@/components/feed/otherfeeds/Mainfeeds.vue'
import Searchmain from '@/components/feed/searchfeeds/Searchmain.vue'
import axios from 'axios'
import Error from '../components/common/layout/Error.vue'
import eventBus from "@/eventBus.js"
const routes = [
  {
    path: '/login',
    name: 'login',
    component: Login
  },
  {
    path: '/join',
    name: 'join',
    component: Join
  },
  {
    path: '/userfeed/:nickname',
    name: 'userfeed',
    component: UserFeed,
    props: route => ({ nickname: route.params.nickname }), // URL  props 
    meta: { requiresAuth: true }, //   
  },
  {
    path: '/',
    name: 'mainfeed',
    component:Mainfeed,
    meta: { requiresAuth: true }, //   
   
  }
  ,
  {
    path: '/globalfeed',
    name: 'globalfeed',
    component:globalfeed,
    meta: { requiresAuth: true }, //   
   
  }
  ,
  {
    path: '/searchfeed/:type/:keyword',
    name: 'searchfeed',
    component:Searchmain,
    props: route => ({
      type: route.params.type,
      keyword: route.params.keyword
    }), // URL  props 
    meta: { requiresAuth: true }, //   
   
  },
  {
    path: '/:pathMatch(.*)*',
    name: 'error',
    component:Error,
   
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
  scrollBehavior() {
    //    
    return { top: 0 };
  }
})


//   1     = >       
//  ,    ,  1 
router.beforeEach((to, from, next) => {
 
  const token = localStorage.getItem('access');
 
  //     
  if ((to.fullPath === '/login' || to.fullPath === '/join')) {

    if (token) {

      //     
      axios.post('/reissue', {
        headers: {
          'Content-Type': 'application/json'
        }
      })
      .then(()=> {
        //      

        next({ path:  "/"});
      })
      .catch(() => {
        //     
        localStorage.removeItem('requestUrl');
       localStorage.removeItem('access')
       router.push('/login')
    
       
      });
    } else {
      //     
      next();
    }
  } else if (to.matched.some(record => record.meta.requiresAuth)) {
    //    
    if (token) {
      eventBus.emit('getAlarmData');
      next();
    } else {
      
      localStorage.setItem('requestUrl', to.fullPath);//  localstorage  - >    
      next({ name: 'login' });//     
    }
  } else {
    next();
  }
});

export default router
