<template>

  <v-container >

    <v-tabs
      class="to-blackMode"
      fixed-tabs
      v-model="type"
    >
      <v-tab value="NORMAL" @click="changeType('NORMAL')" ><span style="color:floralwhite">{{nickname}}</span> </v-tab>
      <v-tab value="TAGGED" @click="changeType('TAGGED')"><span style="color:floralwhite">{{ nickname }}</span>  </v-tab>
    
    </v-tabs>
    <v-divider></v-divider>
    <v-progress-linear
          color="white"
          indeterminate
          v-if="isLoading"
         ></v-progress-linear>

    <v-row v-if="!postData.length && !isLoading" style="margin-top: 5px;">
      <v-col class="d-flex child-flex justify-center align-center" cols="12">
           .
      </v-col>
    </v-row>

    <v-row v-if="postData.length " style="margin-top: 1px;" >
      <v-col
        v-for="(post, index) in postData"
        :key="index"
        class="d-flex child-flex"
        cols="4"
      >
        <v-img
          
          :src="post.postImgUrl"
          aspect-ratio="1"
          class="image-container"
          cover
           @click="openPostDetailDialog(post.postNum)"
        >
          <template v-slot:placeholder>
            <v-row align="center" class="fill-height ma-0" justify="center" v-if="isLoading">
              <v-progress-circular color="grey-lighten-5" indeterminate></v-progress-circular>
            </v-row>
          </template>
          <div class="overlay" >
          <span >  <v-icon class="overlay-icon" >mdi-heart</v-icon>
            {{ post.likeCount }}
          </span>
          </div>
        </v-img>
   
      </v-col>
    
    </v-row>
    <div ref="sentinel" class="sentinel" ></div>
  </v-container>
  <PostDetail 
   :value="postDetailDialog"
   :postNum="sendPostNum"
   @postDetailClose="closePostDetailDialog"
   ></PostDetail>
</template>

<script>
import api from "@/api"
import PostDetail from '../post/PostDetail.vue';
import eventBus from "@/eventBus"
export default{
    name:'UserFeedPostList',

  //     
  props:{
    nickname:String,
   
  },
  data(){
    return{
      postData:[], //   
      isLoading:false, //     
      page:1, // 
      observer:null, //intersection observer 
      noMoreNeedData:false, //     api     
      sendPostNum:0, //  
      type:'NORMAL', //   ,     
      isFirstLoad:true //  
    }
  },
  // intersection observer   ref
  computed:{
    sentinel() {
      return this.$refs.sentinel;
    }
  },
  components:{
    PostDetail
  },
    //    , intersection observer 
    mounted(){
      eventBus.on('resetPostList',this.resetData);
    
     this.initObserver(); // IntersectionObserver 
    },
    watch: {
    //   
    nickname: {
      immediate: true, //    
      handler(newNickname, oldNickname) {
        // nickname   
        if (newNickname !== oldNickname) {
          //    
          this.type='NORMAL'
          this.resetData();
        }
      }
    },
    //   (   OR     )
    type: {
      handler(newType,oldType) {
       if(newType!==oldType){
        this.resetData();
       }
             
      }
    }
  },

    //     
    beforeUnmount() {
      eventBus.off('resetPostList',this.resetData);
    if (this.observer) {
      this.observer.disconnect(); 
    }
  },
    methods:{
     //  
    changeType(type) {
      this.type=type;
    },
    //  
    openPostDetailDialog(postNum){
      this.sendPostNum=postNum
      this.postDetailDialog=true;
    },
    //  
    closePostDetailDialog(){
      this.sendPostNum=0;
      this.postDetailDialog=false;
    },
      //   api
      loadPost(){ 
        if(this.noMoreNeedData ||this.isLoading)return 
        //api   
        this.isLoading=true
        //noMoreNeedData true   (    )
        if(!this.isFirstLoad){
          this.page++;// 

        }
        //    
        this.isFirstLoad=false

        api.get(`/feed/userfeed/post/${this.type}/${this.nickname}/${this.page}`)
        .then((res) => {
          //     
         this.postData=[...this.postData,...res.data.reqData]
          //      true    api   
          if(res?.data?.reqData?.length===0){
            this.noMoreNeedData=true;
            this.page--;
          }
        })
        .catch((err) => {
          //  alert
          if(err?.response?.data?.message){
        alert(err?.response?.data?.message)  
        }
        })
        .finally(() => {
          //api false
          this.isLoading = false;
          //     
          this.$emit('childLoadingComplete')
        });
      },
      
    //    
    resetData() {
   
      this.page = 1;
      this.postData = [];
      this.noMoreNeedData = false;
      this.isLoading=false;
      this.observer=null;
      this.sendPostNum=0;

      this.isFirstLoad=true;
     
      this.loadPost();
    },
      //intersectionobserver  
      initObserver() {
      this.$nextTick(() => {
       //ref sentinel 
        const sentinel = this.$refs.sentinel;

        if (sentinel) {
          this.observer = new IntersectionObserver((entries) => {
            //
            entries.forEach(entry => {
              if (entry.isIntersecting) {
               
                this.loadPost(); // Sentinel     
              }
            });
          }, {
            root:null, // viewport root 
            rootMargin: '0px',
            threshold: 0.1 
          });
          this.observer.observe(sentinel); // Sentinel  
        }
      });
    }
    }
}
</script>
