<template>
    <v-dialog v-model="localDialog" max-width="600">

        <v-card 
        prepend-icon="mdi-account"
        title=" "
        class="pa-3"
        >

       <v-divider></v-divider>

        <!-- -->
        <v-progress-linear
          color="black"
          indeterminate
          v-if="loading"
         ></v-progress-linear>

    <v-card-text class="mt-6">
      <!-- -->
             <v-row>
                  <v-col cols="8">
                    <div class="text-subtitle-1 text-medium-emphasis">Email</div>
                      <v-text-field 
                        density="compact"
                        placeholder="Email"
                        prepend-inner-icon="mdi-email-outline"
                        variant="outlined"
                        v-model="email"
                        :rules="emailRules"
                        :error-messages="emailErrors"
                        :disabled="loading"
                      ></v-text-field>
                </v-col>
              </v-row>
      
        <div class="text-subtitle-1 text-medium-emphasis d-flex align-center justify-space-between">
          
        </div>

        <v-row>
            <v-col cols="7">
                  <v-text-field           
                  :rules="nameRules"
                  required
                  :error-messages="nameErrors"
                   density="compact"
                   placeholder=""
                   prepend-inner-icon="mdi-account"
                   variant="outlined"               
                  v-model="name"
                     :disabled="loading"
                  ></v-text-field>
             </v-col>
         </v-row>
        


          <div class="text-subtitle-1 text-medium-emphasis d-flex align-center justify-space-between">
          
          </div>


        <v-row>
            <v-col cols="3">
                <v-select
                  density="compact"               
                  variant="outlined"
                  v-model="firstPhoneNum"
                  :items="items"                         
                  required   
                  :disabled="loading"          
                ></v-select>
           </v-col>

            <v-col cols="3">
              
                <v-text-field               
                  type="text"
                  density="compact"
                  maxlength="4" 
                  v-model="middlePhoneNum"
                  variant="outlined"  
                  :disabled="loading"       
                ></v-text-field>

            </v-col>

              <v-col cols="3">
                  <v-text-field
                     maxlength="4" 
                     type="text"
                     density="compact"
                     variant="outlined"
                     v-model="lastPhoneNum"
                       :disabled="loading"
                  ></v-text-field>

              </v-col>

    </v-row>
        
              
        </v-card-text>

    
        <v-card-actions>
          <v-spacer></v-spacer>          
              <v-btn
                text="Close"
                variant="plain"
                :disabled="loading"
                @click=closeDialog()
              ></v-btn>

              <v-btn
                color="primary"
                text="Submit"
                variant="tonal"
                @click=submitFindPwd
                :disabled="loading"
              ></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </template>
  
  <script>
  import api from "@/api"
  export default {
    name: 'FindPwdComponent',
    data(){
      return{  
        items:['010','011','016','017','019'],//     
        firstPhoneNum:'010', //  010 
        middlePhoneNum:'',//  
        lastPhoneNum:'',//  
        name:'',//
        email:'', //  
        loading:false,//  

        
        emailRules: [
                v => !!v || '  .', //   
                v => v.trim().length > 0 || '   .', //   
                v => !/\s/.test(v) || '   .', //  
                v => /.+@.+\..+/.test(v) || '   .' //@ .   
        ],
        nameRules:[
        v => !!v || '  .', //    
        v => v.trim().length > 0 || '    .', //   
        v => !/\s/.test(v) || '   .', //  
        ],  
        //
         nameErrors:[],// 
         emailErrors: [] //  
      }
    },

    props: {  
      value: {//    
        type: Boolean,
        required: true
      }
    
    }
    ,computed:{
        localDialog:{//props        
            get(){
                return this.value
            }
        }
    },      
    methods: {
      //  
      closeDialog() {
        // 
        this.firstPhoneNum='010', 
        this.middlePhoneNum=''
        this.lastPhoneNum=''
        this.name=''
        this.email=''
        //       
        this.$emit('findPwdClose'); 
      }, 
      //rules   
      validateField(value, rules) { 
          return rules
            .map(rule => rule(value))
            .filter(error => typeof error === 'string');
        },
        //  
        submitFindPwd(){
          //    
          if(this.loading===true){  
          return;
          }
          // 
          this.nameErrors = this.validateField(this.name, this.nameRules); 
          //  
          this.emailErrors = this.validateField(this.email, this.emailRules); 

           //    
          if (this.nameErrors.length > 0  || this.emailErrors.length) {
        
             return;// axios    
          }
            
                //  
                const fullPhoneNum= (this.firstPhoneNum+this.middlePhoneNum+this.lastPhoneNum); 
                //    validation

                if (fullPhoneNum.length !== 11) {
                  alert('  11 .');
                  return;
                }

                //    
                if (/[-]/.test(fullPhoneNum)) {
                  alert('     .');
                  return;
                }

                //    
                if (/\s/.test(fullPhoneNum)) {
                  alert('     .');
                  return;
                }

                //      
                if (!/^\d+$/.test(fullPhoneNum)) {
                  alert('    .');
                  return;
                }


                //   
                this.loading=true;
                //   api   
                api.post('/members/findPassword',{
                  name:this.name,// 
                  phone:fullPhoneNum,//  
                  email:this.email// 
                })
                .then(()=>{
                    // 
                    alert('   .')
                     //              
                    this.closeDialog()
                  

                })
                .catch((err)=>{
                //     
                if(err?.response?.data?.message){
                alert(err?.response?.data?.message)  
                }
                
                })
                .finally(()=>{
                  this.loading=false // 
                })
               
        }
        
    }
  }
  </script>