<template>
       
    <div class="text-center">
      <v-dialog
        v-model="localDialog"
        max-width="600"    
      >
        
         <v-progress-linear
          color="cyan"
          indeterminate
          v-if="isLoading"
         ></v-progress-linear>

        <v-card
          prepend-icon="mdi-account"
          title=" "
          class="to-blackMode"
        >

          <v-card-text >

            <div class="text-subtitle-1 text-medium-emphasis d-flex align-center justify-space-between " >
               <span class="to-blackMode">  </span>
            </div>

           <!---->
            <v-row>
      
            <v-col cols="7">
                <v-text-field
                  density="compact"
                  placeholder=" "
                  prepend-inner-icon="mdi-account"
                  variant="outlined"
                  v-model="nickname"  
                  :rules="nickNameRules"
                  required
                  :error-messages="nickNameErrors"
                  :readonly="isNickNameReadonly"
                  :disabled="isLoading"
                ></v-text-field>
            </v-col>

        <!-- -->
          <v-col cols="4">
                <v-btn
                  height="40"
                  min-width="50"
                color="blue"
                @click="nickNameValidation"
                :disabled="isLoading"
                >
                  
                </v-btn>
          </v-col>

         </v-row>  

            <div class="text-subtitle-1 text-medium-emphasis d-flex align-center justify-space-between">
              <span class="to-blackMode"> </span>
           </div>

        <!---->
        <v-row>
            <v-col cols="12">
              <v-text-field
                :append-inner-icon="visible ? 'mdi-eye-off' : 'mdi-eye'"
                :type="visible ? 'text' : 'password'"
                density="compact"
                placeholder=" "
                prepend-inner-icon="mdi-lock-outline"
                variant="outlined"
                @click:append-inner="visible = !visible"
                v-model="password"     
                :rules="passwordRules"
                required
                :error-messages="passwordErrors"
                :disabled="isLoading"
              ></v-text-field>
            </v-col>
        </v-row>

    </v-card-text>
  
          <v-divider></v-divider>
  
          <v-card-actions>
            <v-spacer></v-spacer>
  
            <v-btn
              text="Close"
              variant="plain"
              @click="cancle"
              :disabled="isLoading"
            ></v-btn>
  
            <v-btn
              color="primary"
              text="Save"
              variant="tonal"
              @click="submitNickChange()"
              :disabled="isLoading"
            ></v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>

    </div>
  </template>

  <script>
  import api from '@/api';
export default {
    name: 'ChangeNickname',
  
   //      
    props: {
      value: {
        type: Boolean,
        required: true
      }
    
    },
    data(){

    return{
       nickname:'',//  
       password:'',// 
       nickNameRules:[
       v => !!v || '  .', //    
              v => v.trim().length > 0 || '    .', //   
              v => !/\s/.test(v) || '   .', //  
              v => !/[-|-|-]/.test(v) || '    .', //   
              v => /^[a-zA-Z0-9_ ]+$/.test(v) || ' _     .', //  
              v => v.length <= 15 || '  15   .', //   
          ] ,
       passwordRules: [
                v => !!v || '  .', //   
                v => v.trim().length > 0 || ' .', //  
                v => !/\s/.test(v) || '   .', //  
              ],
       nickNameErrors:[],//  
       passwordErrors:[],//  
       isNickNameClear:false,//   
       isNickNameReadonly:false,//   readonly  
       isLoading:false //   = >     
                    
    }
 },
 computed:{
  //    (true/false)  , props value   .
    localDialog:{
        get(){
            return this.value 
        }
    }
 },   
   
    methods: {
        //rules  
        validateField(value, rules) {  
            return rules
              .map(rule => rule(value))
              .filter(error => typeof error === 'string');
        }, 

        //   
        nickNameValidation(){
                //  return
                if(this.isLoading===true){
                  return;
                }

                //    
                this.nickNameErrors = this.validateField(this.nickname, this.nickNameRules); 

                //       return
                if (this.nickNameErrors.length > 0 ) {
                    
                     return;// axios   
                  
                }

                this.isLoading=true;//     true
                  
                //    
                  api.post('/members/nickValidate',{
                  nickname:this.nickname// 
                  })
                  .then(()=>{
                    // 
                    alert(' .')
                    this.isNickNameClear =true; //   
                    this.isNickNameReadonly=true;//    
                  
                   })
                  .catch((err)=>{
                    //     
                    if(err?.response?.data?.message){
                        alert(err?.response?.data?.message)  
                    }
                  
                   })
                 .finally(()=>{
                  //  false 
                  this.isLoading=false;
                   })
               
               
                

            
        },
      //    
      closeDialog() {
        // 
        this.nickname=''
        this.nickNameErrors=[]
        this.isNickNameClear=false
        this.isNickNameReadonly=false
        this.password=''

        this.$emit('changeNickname');//     
      },
      //  
      submitNickChange(){
        if(this.isLoading){
          return
        }
        //    return
        if(!this.isNickNameClear){
          alert('   .')
          return
        } 

        this.isLoading=true

        //   
        this.passwordErrors = this.validateField(this.password, this.passwordRules); 

        if (this.passwordErrors.length > 0 ) { //       return
            
            return;// axios    
        }

        // validation  
            
        //   api  ==>        
        api.put("/setting/nickChange",{
          nickname:this.nickname,// 
          password:this.password//
        })
        .then((res)=>{
        //     
        //why?   jwt           ,    
        // .       .
        const newToken =res.headers['access'];
        localStorage.setItem('access', newToken);
          alert(' .')
          //  
          this.closeDialog()
        })
        .catch((err)=>{
          //    
            if(err?.response?.data?.message){
              alert(err?.response?.data?.message)  
            }        
        })
        .finally(()=>{
                  //  false 
                  this.isLoading=false;
        })
       
      },
      cancle(){
        //
        this.closeDialog()
      }

    }
  }
  </script>