<template>
    : {{ minutes }}:{{ seconds }}
 
  </template>
  
  <script>
export default {
  data() {
    return {
      timeLeft: 180, //3 
      endTime: null, //  
      timer: null, //   
    };
  },
  computed: {
    minutes() {
      return Math.floor(this.timeLeft / 60);//  
    },
    seconds() {//  
      return String(this.timeLeft % 60).padStart(2, '0'); 
    },
  },
  // 
  methods: {
    resetTimer() {
        this.timeLeft = 180; // 3 (180)
        this.startTimer(); //  
    },
    // 
      startTimer() {
        if (this.timer) {
          clearInterval(this.timer);//  = > 
        }

        this.endTime = Date.now() + this.timeLeft * 1000; //  +180  
        this.timer = setInterval(() => {
        const currentTime = Date.now();// 
        this.timeLeft = Math.max(Math.floor((this.endTime - currentTime) / 1000), 0); //  
        
        if (this.timeLeft === 0) { //  0   x 
          clearInterval(this.timer);
        }
      }, 1000);//1  
    },
  },
  //mount  
  beforeUnmount() {
    if (this.timer) {
      clearInterval(this.timer); //    = >   x 
    }
  },
};
</script>

<style scoped>
p {
  font-size: 20px;
  font-weight: bold;
}
</style>