<template >

  <v-card >
   
      <v-navigation-drawer
       class="to-blackMode"
       style="border-right:floralwhite 1px solid"
      >
          <template v-slot:prepend >
            <v-list-item
            >
              <!---->
              <v-img
                class="mx-auto"
                max-width="60"
                src="https://ta9termproject.s3.ap-northeast-2.amazonaws.com/logo2.png"
            ></v-img> 
          
          </v-list-item>

          
          </template>

        <v-divider></v-divider>
          <!--  -->
        <v-list density="compact" nav        
        >
        
          <v-list-item 
          prepend-icon="mdi-pencil" 
          title=" " 
          value="1" 
          @click="postInsertDialogOpen"
         
          ></v-list-item>
              <router-link to="/globalfeed" class="router-link"> 
                <v-list-item prepend-icon="mdi-account-group-outline"  value="2" title=" " ></v-list-item></router-link>
              <router-link to="/" class="router-link"> <v-list-item prepend-icon="mdi-home-city"  title=" " value="3"></v-list-item></router-link>
                <template v-if="memberData.nickname">
                  <router-link :to="{ name: 'userfeed', params: {nickname:this.memberData.nickname }}" class="router-link" value="4"><v-list-item prepend-icon="mdi-home" title=" " value="home"></v-list-item></router-link>
                </template>
               <v-list-item prepend-icon="mdi-heart-outline" @click="AlarmDialogOpen()" title=""  value="5" v-if="noreadAlarmCount===0"></v-list-item>
               <v-list-item  @click="AlarmDialogOpen()" value="5" v-if="noreadAlarmCount>0">
              <v-icon color="red">
                mdi-heart
              </v-icon>
                <span style="font-size: 14px; margin-left: 32px; color: #FFCCCC;"></span>
                <span style="font-size: 10px; opacity: 0.7; margin-left: 3px ;color: #FFCCCC;" >(  {{ noreadAlarmCount }})</span>

               </v-list-item>
                <v-list-item prepend-icon="mdi-wrench" title="" value="6" @click="settingDialogOpen()"></v-list-item>
                <v-list-item prepend-icon="mdi-lock" title="" value="7" @click="logout()"></v-list-item>
        </v-list>

      </v-navigation-drawer>
    
     
  </v-card>
  <!--    -->
  <SettingComponent 
  v-model:value="settingDialog"
  v-model:memberData="memberData"
  @updateSideMenuInfo="reGetInfo"
  @settingClose="ifSettingClose">
 </SettingComponent> 
 <AlarmList
 v-model:value="AlarmDialog"
 @closeAlarm="AlarmDialogClose"
 >
 </AlarmList>
<!--    -->
  <PostInsert v-model:value="postInsertDialog" @postInsertClose="postInsertDialogClose"></PostInsert>
</template>

<script>
import PostInsert from "@/components/feed/post/PostInsert.vue";
import SettingComponent from "../setting/Setting.vue"
import AlarmList from "../utill/AlarmList.vue";
import api from "@/api"
import eventBus from "@/eventBus"

export default{

  data(){
    return{
      settingDialog:false, //  
      postInsertDialog:false, //    
      memberData:{}, //
      isLoading:false ,//
      AlarmDialog:false,//    
      selectedValue:null,
      noreadAlarmCount:0,

    }
  },
 
  components:{
    SettingComponent,// 
    PostInsert,//  
    AlarmList
  },
 mounted(){
  
  this.getInitInfo()
  eventBus.on('getAlarmData',this.alarmCountGet);
 },
 unmounted(){
  eventBus.off('getAlarmData',this.alarmCountGet);
 },
 //
    watch:{
      //     app.vue( )
      isLoading(newLoading){
        if(newLoading){
          this.$emit('childLoading')
        }
      },
      //        
      settingDialog(){
        this.getInitInfo()
      }
     
    },
  methods:{
   alarmCountGet(){
    if(this.isLoading)return

      this.isLoading=true;
      api.get('/alarm')
      .then((res)=>{
        this.noreadAlarmCount=res?.data?.reqData
        console.log(this.noreadAlarmCount)
      })
      .catch((err)=>{
             if(err?.response?.data?.message){
              alert(err?.response?.data?.message)  
            }
      })
      .finally(()=>{
        this.isLoading=false
      })
   },
    resetValue(){
     
     this.selectedValue=10
    },
    //      
    reGetInfo(changeState){
      this.memberData.locked=changeState //     ()
    },
    //   /    ==>      /    
    getInitInfo(){ //   ( / ) 
      
      if(this.isLoading===true){
        return;
      }

      this.isLoading=true
      // ,   api  (     api  )
      const token= localStorage.getItem('access')
      if(!token){

        return
      }
        //  api  
        api.get('/setting')
        .then((res)=>{
          // 
          this.memberData=res?.data?.reqData//   
          
          
        })
        .catch((err)=>{
          if(err?.response?.data?.message){
            alert(err?.response?.data?.message)  
            }
          this.$router.push({name:'error'})
        })
        .finally(()=>{
          this.isLoading=false
          this.alarmCountGet()
        })
      
       
      },
    // ,   
    ifSettingClose(){
      this.settingDialog=false 
    },
    settingDialogOpen(){
     this.getInitInfo()
      this.settingDialog=true
    },
    postInsertDialogOpen(){
      this.postInsertDialog=true
    },
    postInsertDialogClose(){
      this.postInsertDialog=false
    },
    AlarmDialogOpen(){
      this.AlarmDialog=true
    },
    AlarmDialogClose(){
      this.AlarmDialog=false
    },
    //  
    logout(){
      if(this.isLoading){
        return
      }

      this.isLoading=true
      // api  
      api.post('/logout')
      .then(()=>{
        //    ,    
        localStorage.removeItem('requestUrl');   
        localStorage.removeItem('access');
        this.$router.push('/login');

          
        })
        .catch((err)=>{
          //   
          if(err?.response?.data?.message){
            alert(err?.response?.data?.message)  
          }
          
          localStorage.removeItem('access')//   
          this.$router.push('/login');//   
        })
        .finally(()=>{
          this.isLoading=false
        })
         
    }
  }
}

</script>

<style>
.router-link {
  text-decoration: none; /*     */
  color: inherit; /*     v-list-item      */
}

</style>