<template  >
  <!--  -->
  <v-dialog v-model="localDialog" max-width="1100" class="modal" >
      <v-row v-if="isLoading" class="loading-overlay">
        <v-col class="d-flex justify-center align-center">
          <v-progress-circular indeterminate color="primary"></v-progress-circular>
        </v-col>
      </v-row>

    <v-card class="to-doubleBlack" v-if="!isLoading">           
      <v-container fluid >
        <v-row no-gutters >         
          <v-col cols="6" class="image-section">
       
      <!--  -->
      <v-carousel show-arrows="hover" >
        <div  v-for="(image,index) in imgList" :key="index">
          <v-carousel-item
             width="550px"
             height="500px"
             style="object-fit: contain;"
            :src="image"
            cover
          ></v-carousel-item>
        </div>
      </v-carousel>
          </v-col >

          <!--    (,  ) -->
          <v-col cols="6" class="content-section " >
            <v-icon
              class="close-icon"
              @click="closeDialog"
              color="white"
            >mdi-close</v-icon><!-- x - >  -->

           
            <v-card-text  ><!--    ,, , ,    -->
            
              <v-row no-gutters >
              
                <v-row cols="12" class="ml-1">
                  <v-col cols="1"  style="cursor:pointer" @click="goOtherUserfeed(postDetailData.nickname)">
                    <v-avatar :image="postDetailData.profile" size="35" class="avatar"
                    v-if="postDetailData.profile !== null" ></v-avatar>
                    <v-avatar :image="require('@/assets/ikmyung.png')"  
                     size="35" class="avatar" v-if="postDetailData.profile === null" ></v-avatar>
                  </v-col>
                  <v-col cols="5" class="mt-1 ml-1" >
                    <span class="large-font " style="cursor:pointer" @click="goOtherUserfeed(postDetailData.nickname)">{{postDetailData.nickname }}</span>
                  </v-col>
                  <v-col cols="5" class="mt-2" v-if="postDetailData.editDate===null">
                    <span  style="color: white; opacity: 0.7;">:&nbsp;&nbsp;{{postDetailData.createDate }}</span>
                  </v-col>

                  <v-col cols="5" class="mt-2" v-if="postDetailData.editDate!==null">
                    <span  style="color: white; opacity: 0.7;">:&nbsp;&nbsp;{{postDetailData.editDate }}</span>
                  </v-col>
                </v-row>
              <v-divider></v-divider>
                <v-col cols="12" style="height: 100px;" class="mt-1">
                  <v-card class="to-blackMode"  elevation="1" max-width="100%" height="100%" >
   
                    <v-card-text v-if="postDetailData.content!==null">
                  
                     {{ postDetailData.content }}
                    </v-card-text>

                    <v-card-text v-if="postDetailData.content===null">
                         .
                    </v-card-text>
                  </v-card>

                </v-col>
              
                <v-col cols="12" class="mt-1" style="height: 130px;"  >
                  <v-card class="to-blackMode scroll-container" elevation="1" max-width="100%" height="100%" >
   
                    <v-card-item>
                     
                      <v-card-subtitle></v-card-subtitle>
                    </v-card-item>     
                     <v-card-text v-if="postDetailData.hobbyList!==null" >  
                      <span   v-for="(hobby,index) in postDetailData.hobbyList" :key="index">
                        <v-chip
                          class="ma-2 clickcursor"
                          color="green"
                          style="font-weight: bold;"
                          label    
                          @click="goSearchFeed(hobby)"                                            
                          >
                           <v-icon icon="mdi-label " start></v-icon>
                          #{{ hobby }}
                        </v-chip>
                      </span> 
                  </v-card-text>
                    <v-card-text v-if="postDetailData.hobbyList===null">
                        .
                    </v-card-text>
                  </v-card>

                </v-col>
                
                <v-col cols="12" class="mt-2" style="height: 100px;">
                  <v-card class="to-blackMode  scroll-container" elevation="1" max-width="100%" height="100%" >
   
                    <v-card-item>
                     
                      <v-card-subtitle></v-card-subtitle>
                    </v-card-item>                 

                    <v-card-text v-if="postDetailData.tagList!==null" >           
                      <span  v-for="(tag,index) in postDetailData.tagList" :key="index">  
                  
                        <v-chip                         
                            class="ma-2 custom-chip"                       
                            label
                            style="cursor: pointer;"
                            @click=" goOtherUserfeed(tag.nickname)"
                          >
                          <v-avatar :image="tag.profile" size="20" class="avatar"
                              v-if="tag.profile !== null" ></v-avatar>
                              <v-avatar :image="require('@/assets/ikmyung.png')"  
                              size="20" class="avatar" v-if="tag.profile === null" ></v-avatar>
                          <span style="margin-right:10px;margin-left: 5px; font-weight: bold;"> {{ tag.nickname }}</span>
                          
                          </v-chip>
                    
                      </span>
                  </v-card-text>

                    <v-card-text v-if="postDetailData.tagList===null">
                       .
                    </v-card-text>
                  </v-card>

                </v-col>
                <v-divider style="color: aliceblue;"></v-divider>
                <v-col cols="12" class="like-icon-col  to-blackMode">
                  <v-icon
                    style="cursor: pointer;"
                    :class="{'red--text': postDetailData.liked, 'grey--text': !postDetailData.liked}"
                    large
                    size="30"
                    :color="postDetailData.liked?'red':''"
                    @click="doPostLike()"
                  >
                    {{ postDetailData.liked ? 'mdi-heart' : 'mdi-heart-outline' }}
                  </v-icon>
                  <span style="margin-left: 20px; font-size: 16px;">{{postDetailData.likeCount}} .</span>
                 
                  <span style="margin-left: 200px;" v-if="postDetailData.itsMe">
                    <v-btn class="ms-5" icon="mdi-pencil" size="30" color="black" @click="postEditOpen"></v-btn><!-- -->
                  </span>
                  
                  <span style="margin-left: 8px;"  v-if="postDetailData.itsMe">
                  <v-btn icon="mdi-delete-outline"  color="red" size="30"
                  @click="openAlert('   ?','postDel')"
                  ></v-btn> <!-- -->
                </span>
                </v-col>
                
               
              </v-row>
             
            </v-card-text>
          </v-col>
        </v-row>
      </v-container>
    </v-card>
  </v-dialog>

  <!--   -->
    <PostEdit v-model:value="postEditDialog"
      @postEditClose="postEditClose"
      @updateComplete=" closeDialog" 
      :postNum="postDetailData.postNum"
      v-if="postNum!==0">
    </PostEdit>
<!-- -->
    <Alert
        v-model:value="isAlertOpen"
        v-model:altype="alertType"
        v-model:message="alertMessage"
        @closeAlertDialog="closeAlert"
      >
    </Alert>
</template>

<script>

import Alert from "@/components/common/utill/Alert.vue"
import PostEdit from "./PostEdit.vue"
import api from "@/api";
import eventBus from "@/eventBus.js"
export default {
  name: 'PostDetail',

  props: {
    value: {//  ,  
      type: Boolean,
      required: true
    },
    //  
    postNum:{
      type:Number,
      required: true
    }
  },
  data() {
    return {
      postEditDialog:false, // 
      postDetailData:{},//  
      imgList:[], //  
      isLoading:false, //   
      isLikeLoading:false, //    (    )
      alertType:'', //    (   )
      isAlertOpen:false,//   
      alertMessage:'' ,//  
          
    }
  },
  computed: {
    //  open,close 
    localDialog: {
      get() {
        return this.value;
      }
    }
  },
  //
  watch:{
    //     
    postNum(newValue){
     
      if(newValue>=1){
        //   
        this.callPostDetailData(newValue)
      }
     
    }
  },
  methods: {
    
    //   
    callPostDetailData(newValue){
      //  return 
      if(this.isLoading)return
      // true
      this.isLoading=true

      api.get(`post/${newValue}`)
      .then((res)=>{
        //  
        this.imgList=res?.data?.reqData?.imgList
        //   
        this.postDetailData=res?.data?.reqData
       
      
      })
      .catch((err)=>{
        if(err?.response?.data?.message){
        alert(err?.response?.data?.message)  
        }
        this.closeDialog()
      })
      .finally(()=>{
        this.isLoading=false
      })
    },
    //  
    doPostLike(){
      //  return (  )
      if(this.isLikeLoading)return
      // true
      this.isLikeLoading=true
      api.post('/post/like',{
        // 
        postNum:this.postDetailData.postNum,
        //    ( )
        liked:this.postDetailData.liked

      })
      .then((res)=>{
        //      = >   dom 
        const resLiked=res?.data?.reqData?.liked;
        //      
        const resLikeCount= res?.data?.reqData?.likeCount;

        this.postDetailData.liked=resLiked
        this.postDetailData.likeCount=resLikeCount 

      })
      .catch((err)=>{
        if(err?.response?.data?.message){
        alert(err?.response?.data?.message)  
        }
        })
        .finally(()=>{
          this.isLikeLoading=false
        })
    },
    //  
    closeDialog() {
      this.$emit('postDetailClose', false); 
    },
    //    
    postEditOpen(){
      this.postEditDialog=true;
    },
    //    
    postEditClose(){
      this.postEditDialog=false;
      
    },
    //   
    openAlert(message,type){

    this.alertMessage=message //    , 
    this.alertType=type//   ,  

    this.isAlertOpen=true //   
  },

  closeAlert(isOk,type){//ok or no  ,    (,  )
        
        //ok  ,          
        if (isOk&&type==='postDel') {
          //  
          if(this.isLoading)return
          this.isLoading=true
          //  api  
          api.delete('/post',{
            data:{
              //  
              postNum:this.postNum
            }
          })
          .then(()=>{
            //      ==>    
            this.closeDialog()
            alert('  .')
            eventBus.emit('resetPostList');
          })
          .catch((err)=>{
            if(err?.response?.data?.message){
            alert(err?.response?.data?.message)  
            }
          })
          .finally(()=>{
            this.isLoading=false
            this.closeDialog()
          })
       
        }

        //ok  ,    
        if(!isOk&&type==="postDel"){
           
          this.isAlertOpen=false//  

        }

        this.isAlertOpen=false// no      
       },
    //     
    goOtherUserfeed(nickname) {//  

      this.closeDialog(); //   = > /   
      this.$emit('closeAlarm')
      this.$nextTick(() => {
        this.$router.push({ name: 'userfeed', params: { nickname: nickname } }); //   (  param)
      });
    },
    goSearchFeed(hobby){
      this.closeDialog(); //   = > /  
      this.$emit('closeAlarm') 
        this.$router.push({
          name: 'searchfeed',
          params: { type:'hobby',keyword:hobby}
        });
      },
  
  },
  components: {
    PostEdit,
    Alert
  }
}
</script>

<style scoped>
.modal {
  max-height: 90vh;
  overflow: hidden;
}

.image-section {
  padding: 0;
  position: relative;
}

.content-section {
  padding: 16px;
  position: relative;
}

.close-icon {
  position: absolute;
  top: -10px;
  right: -10px;
  font-size: 30px;
  cursor: pointer;
  z-index: 1; 
}
.loading-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.7) !important;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000; 
}
.overlay-text {
  position: absolute;
  top: 20px; 
  left: 20px; 
  right: 20px; 
  color: white; 
  padding: 10px;
  border-radius: 4px; 
  font-size: 24px;
  z-index: 2; 
  white-space: normal; 
 
}

.post-content-col {
  flex: 1;
  padding-right: 16px;
}

.like-icon-col {
  display: flex;
  align-items: center;
  padding-right: 16px;
  font-size: 14px;
}

.comment-icon-col {
  display: flex;
  align-items: center;
  font-size: 14px;
}

.username {
  font-weight: bold;
  display: block;
}

.post-text {
  width: 520px;
  height: 150px;
  display: block;
  margin-top: 8px;
  padding: 16px;
  background-color: #2C2C2C;
  color: aliceblue;
  border-radius: 4px; 
}

.hobby-text {
  width: 520px;
  height: 50px;
  display: block;
  margin-top: 8px;
  padding: 16px;
  background-color: #2C2C2C;
  color: aliceblue;
  border-radius: 4px; 
}

.v-divider {
  margin: 16px 0;
}
.scroll-container {

  overflow-y: auto;
}

.scroll-container::-webkit-scrollbar {
  width: 12px; 
}


.scroll-container::-webkit-scrollbar-track {
  background: #f1f1f1; 
  border-radius: 10px; 
}


.scroll-container::-webkit-scrollbar-thumb {
  background: #888; 
  border-radius: 10px; 
}


.scroll-container::-webkit-scrollbar-thumb:hover {
  background: #555; 
}
.clickcursor{
cursor: pointer;
}
.large-font {
  font-size: 18px; 
  color:antiquewhite
}
</style>
