<template>
 
<v-dialog activator="parent" v-model="localDialog" max-width="500">
  
          <template v-slot:default="{ isActive }">
              
               <v-progress-linear
                  color="cyan"
                  indeterminate
                  v-if="isLoading"
                ></v-progress-linear>

            <v-card rounded="lg" class="to-blackMode">
              <v-card-title class="d-flex justify-space-between align-center">
                <div class="text-h5 text-medium-emphasis ps-2">
                 <span style="color: white;">  </span>
                </div>

                <v-btn  
                  variant="text"
                  @click="isActive.value = false"
                  :disabled="isLoading"
                ></v-btn>
              </v-card-title>

              <v-divider class="mb-4"></v-divider>

              <v-card-text>
             
                  <div class="mb-2"> </div>

                    <v-textarea
                      maxlength="100"
                      class="mb-2"
                      rows="3"
                      variant="outlined"
                      persistent-counter
                      no-resize
                    :disabled="isLoading"
                      v-model="introduce"
                    ></v-textarea>

               

               
              </v-card-text>

              <v-divider class="mt-2"></v-divider>

              <v-card-actions class="my-2 d-flex justify-end">
                <v-btn
                  class="text-none"
                  rounded="xl"
                  text="Cancel"
                  @click="closeDialog()"
                  :disabled="isLoading"
                ></v-btn>

                <v-btn
                  class="text-none"
                  color="primary"
                  rounded="xl"
                  text="Send"
                  variant="flat"
                  @click="UpdateIntroduceSubmit()"
                  :disabled="isLoading"
                ></v-btn>
              </v-card-actions>
            </v-card>
          </template>
        </v-dialog>

</template>

<script>
import api from "@/api"
export default {
  name: 'FeedEdit',

  props: {
    value: {//     
      type: Boolean,
      required: true
    }
  
},data(){
  return{
     originalIntroduce:'',//  
     introduce:'', //   
     isLoading:false  //   
  }
}
,computed:{
  localDialog:{
      get(){
          return this.value //     (true/false)  , props value   .
      }
  }
},
watch: {
  // ,  
    value(newVal) {
      //  
      if(newVal===true){
        //   
        this.getOriginalIntroduce()
      }
      
    }
  },
mounted() {
  
},
  methods: {
    closeDialog() {
      this.$emit('feedEditClose');//     
    },
    //    
    getOriginalIntroduce(){
    //   
    if(this.isLoading===true){
      return
    }
    //  
    this.isLoading=true
    //    API 
    api.get('/feed/introduce')
    .then((res)=>{
      //  
      this.introduce=res?.data?.reqData?.introduce
      this.originalIntroduce=res?.data?.reqData?.introduce    

    })
    .catch((err)=>{
      if(err?.response?.data?.message){
        alert(err?.response?.data?.message)  
        }
      this.closeDialog()
    })
    .finally(()=>{

      this.isLoading=false
    })
   },
   //  
   UpdateIntroduceSubmit(){
    
    //         
    if(this.introduce===this.originalIntroduce){
      alert('  .')
      return
    }
    //  '' NULL  
    if(this.introduce===''){
      this.introduce=null
    }
      //     
      this.isLoading=true;
      
      api.put('/feed/introduce',{
       introduce:this.introduce
      })
      .then(()=>{
        alert(' .') 
      })
      .catch((err)=>{
        if(err?.response?.data?.message){
        alert(err?.response?.data?.message)  
        }
      })
      .finally(()=>{
        this.isLoading=false
        this.closeDialog()
      })

   } 
  }
}
</script>

