<template>
    
  
      <v-dialog
        v-model="localDialog"
        width="auto"
      >
        <v-card
          max-width="400"
         class="to-blackMode"
        :text="message"  
        >
 
            <template v-slot:actions>
              <v-btn
                color="gray"
                style="opacity: 0.7;"
                text="NO"
                @click="closeDialog(false,this.altype)"
              ></v-btn>
              <v-btn
                color="red"
                text="Ok"
                @click="closeDialog(true,this.altype)"
              ></v-btn>
             

            
            
            </template>
            
        </v-card>
      </v-dialog>
   
  </template>
  <script>
 
  export default {
    name: 'Alert',
  

    props: {
      value: {//      
        type: Boolean,
        required: true
      },
      message:{ //    
        type: String,
        required: true
      },
      altype:{  //    
        type: String,
        required: true
      }
    
},data(){
    return{
     
    }
}
 ,computed:{
    localDialog:{
        get(){
            return this.value //     (true/false)  , props value   .
        }
    },
    getType:{
      get(){
            return this.altype //    alert 
      }
    } ,
    getMessage:{
      get(){
        return this.message
      }
    }
   
  }

  ,
    methods: {
      closeDialog(isOK,type) { 
       
        this.$emit("closeAlertDialog",isOK,type)//     ,   ,     
      },
    
    }
}
  </script>

 
